import React from "react";
import { useSelector } from "react-redux";
import CartPageButton from "./cartPageButton";
function CartProduct({ product, sku }) {
  // const decrementProduct = () => {
  //   dispatch(actions.decrementProduct(item.id, item.sku_id));
  // };
  // console.log(loading, item);
  // useEffect(() => {
  //   return () => toast.dismiss("Out Of Stock");
  // });
  // const incrementProduct = () => {
  //   setloading(() => true);
  //   if (item.stock === item.quantity) {
  //     toast("Out Of Stock", {
  //       position: "top-center",
  //       autoClose: 3000,
  //       type: "info",
  //       hideProgressBar: false,
  //       closeOnClick: true,
  //     });
  //     return;
  //   }
  //   dispatch(actions.incrementProduct(item.id, item.sku_id));
  // };
  const quantity = useSelector((state) => {
    if (!state.cartBuilder.cart.length) {
      return 0;
    } else {
      if (sku) {
        let q = state.cartBuilder.cart.findIndex((i) => i.sku_id === sku.id);
        if (q === -1) {
          return 0;
        } else {
          return state.cartBuilder.cart[q].quantity;
        }
      } else {
        return;
      }
    }
  });
  return (
    <div className="flex flex-col space-y-1 overflow-auto">
      <div className="flex px-2 pt-1 pb-2 mt-2 space-x-2 border-b border-gray-500 product-detail">
        <div className="w-1/3 image relative pb-0.5 ">
          <img
            src={sku.images[0].image}
            alt={product.name}
            className="absolute object-contain w-full h-full"
          />
        </div>
        <div className="flex flex-col w-3/4 space-y-1">
          <div className="flex flex-wrap flex-grow-0 font-semibold ">
            {product.name}
          </div>

          <div className="flex space-x-2 text-gray-900">
            <span>{sku.sp}</span>
            <span>&#215; {quantity}</span>
            <span className="px-1 text-gray-700 bg-gray-400 rounded-lg">
              {quantity}
            </span>
          </div>

          {sku.discount > 0 ? (
            <div className="flex items-center initial-price">
              <span className=""> &#8377; </span>
              <span className="line-through text">{sku.mrp}</span>
              <div className="flex px-1 ml-1 space-x-1 text-white bg-red-400">
                <span>{sku.discount.toFixed(2)}%</span>
                <span>OFF</span>
              </div>{" "}
            </div>
          ) : null}

          <div className="flex items-center justify-between">
            <div className="flex items-center ml-4 font-extrabold">
              <span> &#8377; </span>
              <span>{sku.sp}</span>
            </div>{" "}
            <CartPageButton product={product} sku={sku} quantity={quantity} />
          </div>
        </div>
      </div>
    </div>
  );
}

export default CartProduct;
