import React from "react";

import { Formik, Form } from "formik";
import * as Yup from "yup";
import { useHistory, useLocation } from "react-router-dom";
import Select from "react-select";
import Axios from "axios";
import * as actions from "../store/actions/index";
import { useDispatch, useSelector } from "react-redux";
import { CustomInput } from "../utils/CustomInput";
Axios.defaults.withCredentials = true;

function Addaddress() {
  const userId = useSelector((state) => state.authBuilder.userId);
  let dispatch = useDispatch();
  let history = useHistory();
  let location = useLocation();
  //   async function _submitForm(values, actions) {
  //     await _sleep(1000);
  //     alert(JSON.stringify(values, null, 2));
  //     history.push("/order_confirm");
  //   }

  async function _handleSubmit(values, action) {
    let address;

    address = values;
    address.user_id = userId;
    await Axios.post("https://frozen-reef-15026.herokuapp.com/api/address", {
      address: address,
    });

    let { from } = location.state || { from: { pathname: "/" } };
    dispatch(actions.addAddress(values));
    console.log(from);
    history.replace(from);
  }

  // const CustomInput = ({ label, ...props }) => {
  //   const [field, meta] = useField(props);
  //   return (
  //     <>
  //       <label className="text-gray-600" htmlFor={props.name}>
  //         {label}
  //       </label>
  //       <input
  //         {...field}
  //         className="w-full px-3 py-2 text-sm leading-tight text-gray-700 border rounded shadow appearance-none focus:outline-none focus:shadow-outline"
  //       />
  //       {meta.touched && meta.error ? (
  //         <div className="py-1 pl-4 text-white bg-red-400 ">{meta.error}</div>
  //       ) : null}
  //     </>
  //   );
  // };

  //   function _sleep(ms) {
  //     return new Promise((resolve) => setTimeout(resolve, ms));
  //   }
  let options = [{ label: "King Market", value: "King Market" }];
  return (
    <div>
      <div className="flex flex-col h-screen ">
        <div className="flex items-center text-white bg-red-500 ">
          <div className="flex items-center justify-center py-2">
            <div className="px-2 " onClick={() => history.goBack()}>
              <svg
                fill="currentColor"
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                viewBox="0 0 24 24"
              >
                <path d="M16.67 0l2.83 2.829-9.339 9.175 9.339 9.167-2.83 2.829-12.17-11.996z" />
              </svg>
            </div>

            <div className="pl-4 ">Add Address</div>
          </div>
        </div>
        <div className="flex flex-col flex-1 pt-4 mx-8 ">
          <Formik
            initialValues={{
              city: "",
              pincode: "",
              address: "",

              delivery_area: "",
            }}
            validationSchema={Yup.object().shape({
              city: Yup.string().required("Please enter city Name"),
              address: Yup.string()
                .min(4, "Enter Correct address")
                .required("Please Enter valid address"),
              delivery_area: Yup.string().required(),
              pincode: Yup.number()
                .typeError("That doesn't look like a  number")
                .positive("A pincode can't start with a minus")
                .integer("A pincode can't include a decimal point")
                .min(500000, "Invalid number")
                .max(600000, "Invalid number")
                .required("pincode is required"),
            })}
            onSubmit={(values, actions) => _handleSubmit(values, actions)}
          >
            {({
              values,
              handleSubmit,
              handleChange,
              setFieldValue,
              errors,
              validateForm,
            }) => (
              <Form className="flex flex-col flex-1 overflow-y-auto ">
                <div className="flex flex-col justify-between flex-1 ">
                  <div className="flex flex-col flex-1 pb-10 mx-4 space-y-2 ">
                    <CustomInput
                      label="City"
                      name="city"
                      type="text"
                      onChange={handleChange}
                      value={values.city}
                      symbol={true}
                      placeholder="Enter City Name"
                    ></CustomInput>
                    <Select
                      options={options}
                      placeholder="Select Delivery Area"
                      isMulti={false}
                      value={options.find(
                        (i) => i.value === values.delivery_area
                      )}
                      name="delivery_area"
                      label="delivery_area"
                      onChange={(e) => setFieldValue("delivery_area", e.value)}
                    ></Select>

                    <CustomInput
                      symbol={true}
                      label="Address Detail"
                      name="address"
                      type="text"
                      value={values.address}
                      placeholder="Your House Address Detail"
                    ></CustomInput>
                    <CustomInput
                      symbol={true}
                      label="pincode"
                      name="pincode"
                      type="number"
                      value={values.pincode}
                      placeholder="pincode"
                    ></CustomInput>

                    <div className="flex justify-end py-2">
                      <button
                        type="submit"
                        onClick={() =>
                          validateForm().then((e) => {
                            if (!e) {
                              return _handleSubmit(values);
                            }
                            return;
                          })
                        }
                        className="px-4 py-2 mr-2 text-white bg-green-600 rounded "
                      >
                        Save
                      </button>
                    </div>
                  </div>
                </div>
              </Form>
            )}
          </Formik>
        </div>
      </div>
    </div>
  );
}

export default Addaddress;
