import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import Auth from "./Auth";
import { useSelector, useDispatch } from "react-redux";
import * as actions from "../store/actions/index";
function Drawer({ toggle, onToggle }) {
  let theme = document.body.classList.contains("dark");

  const root = document.getElementById("root");
  const [darkTheme, setDarkTheme] = useState(theme);
  const onClick = () => {
    onToggle();
  };

  useEffect(() => {
    toggle && document.body.classList.add("overflow-hidden");
    root.classList.add("static");

    return () => {
      document.body.classList.remove("overflow-hidden");
      root.classList.remove("static");
    };
  });
  const history = useHistory();

  const auth = useSelector((state) => state.authBuilder.isSignedIn);
  const phoneNumber = useSelector((state) => state.authBuilder.phoneNumber);
  const isAdmin = useSelector((state) => state.authBuilder.isAdmin);
  let dispatch = useDispatch();
  const onSignOutClick = () => {
    dispatch(actions.clearCart());
    dispatch(actions.signOut());
  };

  let classes;
  if (toggle) {
    classes =
      "fixed z-30 h-screen  transition-all  duration-300 ease-out bg-white  dark:bg-mine-shaft-700 dark:text-gray-300   transform translate-x-0  w-60";
  } else {
    classes =
      "fixed z-30 h-screen transition-all   duration-300  ease-out bg-white transform -translate-x-full dark:bg-mine-shaft-700 dark:text-gray-300   w-60";
  }
  return (
    <>
      {toggle && (
        <div
          className="absolute inset-0 z-10 bg-black opacity-25 dark:bg-white dark:opacity-70"
          onClick={onToggle}
        ></div>
      )}
      <div className={` ${classes}`}>
        <div className="flex flex-col h-full ">
          <div className="ease-out border-b-2 border-gray-500 transit ">
            {auth && (
              <div className="flex items-center justify-between py-2 shadow-md dark:bg-mine-shaft-500">
                <span className="pl-2 font-bold">{phoneNumber}</span>
                <span
                  className="pr-2 text-green-400 cursor-pointer "
                  onClick={() => {
                    history.push("/profile");
                  }}
                >
                  Profile
                </span>
              </div>
            )}
            <div className="flex flex-col px-2 pb-6 pt- ">
              <div className="font-bold leading-7">STORE</div>
              <div className=""> KURNOOL</div>
            </div>
            <Auth></Auth>
          </div>
          <div className="flex flex-col py-2 bg-gray-100 border-b border-gray-200 dark:bg-mine-shaft-400 dark:text-gray-100">
            <div
              className="flex items-center py-2 cursor-pointer"
              onClick={onClick}
            >
              <span className="pl-2 pr-2">
                <svg
                  className="text-black dark:text-white"
                  xmlns="http://www.w3.org/2000/svg"
                  x="0px"
                  y="0px"
                  width="24"
                  fill="currentColor"
                  height="24"
                  viewBox="0 0 172 172"
                >
                  <g
                    fill="none"
                    fillRule="nonzero"
                    stroke="none"
                    strokeWidth="none"
                    strokeLinecap="butt"
                    strokeLinejoin="none"
                    strokeMiterlimit="10"
                    strokeDasharray=""
                    strokeDashoffset="0"
                    fontFamily="none"
                    fontWeight="none"
                    fontSize="none"
                    textAnchor="none"
                  >
                    <path
                      d="M0,172v-172h172v172z"
                      stroke="none"
                      strokeWidth="1"
                      strokeLinejoin="miter"
                    ></path>
                    <g
                      stroke="#cccccc"
                      strokeWidth="6.88"
                      strokeLinejoin="round"
                    >
                      <path d="M17.2,17.2h58.48v58.48h-58.48zM96.32,17.2h58.48v58.48h-58.48zM17.2,96.32h58.48v58.48h-58.48zM96.32,96.32h58.48v58.48h-58.48z"></path>
                    </g>
                  </g>
                </svg>
              </span>
              <span>Browse</span>
            </div>
            <div
              className="flex items-center py-2 cursor-pointer "
              onClick={() => history.push("/cart")}
            >
              <span className="pl-2 pr-2 dark:text-white">
                <svg
                  className="dark:text-white"
                  width="24"
                  fill="currentColor"
                  height="24"
                  xmlns="http://www.w3.org/2000/svg"
                  fillRule="evenodd"
                  clipRule="evenodd"
                >
                  <path d="M13.5 21c-.276 0-.5-.224-.5-.5s.224-.5.5-.5.5.224.5.5-.224.5-.5.5m0-2c-.828 0-1.5.672-1.5 1.5s.672 1.5 1.5 1.5 1.5-.672 1.5-1.5-.672-1.5-1.5-1.5m-6 2c-.276 0-.5-.224-.5-.5s.224-.5.5-.5.5.224.5.5-.224.5-.5.5m0-2c-.828 0-1.5.672-1.5 1.5s.672 1.5 1.5 1.5 1.5-.672 1.5-1.5-.672-1.5-1.5-1.5m16.5-16h-2.964l-3.642 15h-13.321l-4.073-13.003h19.522l.728-2.997h3.75v1zm-22.581 2.997l3.393 11.003h11.794l2.674-11.003h-17.861z" />
                </svg>
              </span>
              <span>Shopping Cart</span>
            </div>
            <div
              className="flex items-center py-2 cursor-pointer"
              onClick={() => history.push("/orders")}
            >
              <span className="pl-2 pr-2 ">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  className="w-6 h-6"
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth="2"
                    d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z"
                  />
                </svg>
              </span>
              <span>Order History</span>
            </div>
            <div
              className="flex items-center py-2 cursor-pointer"
              onClick={() => history.push("/wishlist")}
            >
              <span className="pl-2 pr-2 ">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 24 24"
                  className="w-6 h-6"
                  stroke="currentColor"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z"
                  />
                </svg>
              </span>
              <span>Wish List</span>
            </div>

            <div
              className="flex items-center py-2 cursor-pointer"
              onClick={(e) => e.stopPropagation()}
            >
              {!darkTheme && (
                <span className="px-2">
                  <svg
                    onClick={() => {
                      localStorage.setItem("theme", "dark");
                      document.body.classList.add("dark");
                      setDarkTheme(true);
                    }}
                    xmlns="http://www.w3.org/2000/svg"
                    width="24"
                    height="24"
                    fill="none"
                    stroke="currentColor"
                    strokeWidth="2"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    className="inline feather feather-moon"
                  >
                    <path d="M21 12.79A9 9 0 1 1 11.21 3 7 7 0 0 0 21 12.79z" />
                  </svg>
                </span>
              )}
              {darkTheme && (
                <span className="px-2">
                  <svg
                    onClick={() => {
                      localStorage.setItem("theme", "light");

                      document.body.classList.remove("dark");
                      setDarkTheme(false);
                    }}
                    xmlns="http://www.w3.org/2000/svg"
                    width="24"
                    height="24"
                    fill="none"
                    stroke="currentColor"
                    strokeWidth="2"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    className="inline feather feather-sun"
                  >
                    <circle cx="12" cy="12" r="5" />
                    <path d="M12 1v2M12 21v2M4.22 4.22l1.42 1.42M18.36 18.36l1.42 1.42M1 12h2M21 12h2M4.22 19.78l1.42-1.42M18.36 5.64l1.42-1.42" />
                  </svg>
                </span>
              )}

              <span
                onClick={() => {
                  if (darkTheme) {
                    localStorage.setItem("theme", "light");

                    document.body.classList.remove("dark");
                    setDarkTheme(false);
                  } else {
                    localStorage.setItem("theme", "dark");
                    document.body.classList.add("dark");
                    setDarkTheme(true);
                  }
                }}
              >
                {" "}
                Toggle Theme
              </span>
            </div>
            {isAdmin && (
              <>
                {" "}
                <div
                  className="flex items-center py-2 cursor-pointer"
                  onClick={() => history.push("/add-product")}
                >
                  <span className="pl-2 pr-2">
                    <svg
                      className="dark:text-white "
                      fill="currentColor"
                      width="24"
                      height="24"
                      xmlns="http://www.w3.org/2000/svg"
                      fillRule="evenodd"
                      clipRule="evenodd"
                    >
                      <path d="M11 11v-11h1v11h11v1h-11v11h-1v-11h-11v-1h11z" />
                    </svg>
                  </span>
                  <span>Add Product</span>
                </div>
                <div
                  className="flex items-center py-2 cursor-pointer"
                  onClick={() => history.push("/add-product-category")}
                >
                  <span className="pl-2 pr-2">
                    <svg
                      className="dark:text-white "
                      fill="currentColor"
                      width="24"
                      height="24"
                      xmlns="http://www.w3.org/2000/svg"
                      fillRule="evenodd"
                      clipRule="evenodd"
                    >
                      <path d="M11 11v-11h1v11h11v1h-11v11h-1v-11h-11v-1h11z" />
                    </svg>
                  </span>
                  <span>Add Product Category</span>
                </div>
                <div
                  className="flex items-center py-2 cursor-pointer"
                  onClick={() => history.push("/add-category")}
                >
                  <span className="pl-2 pr-2">
                    <svg
                      className="dark:text-white "
                      fill="currentColor"
                      width="24"
                      height="24"
                      xmlns="http://www.w3.org/2000/svg"
                      fillRule="evenodd"
                      clipRule="evenodd"
                    >
                      <path d="M11 11v-11h1v11h11v1h-11v11h-1v-11h-11v-1h11z" />
                    </svg>
                  </span>
                  <span>Add Category</span>
                </div>
              </>
            )}
          </div>
          {auth && (
            <span
              onClick={onSignOutClick}
              className="pt-4 pl-2 text-red-400 cursor-pointer"
            >
              Log Out
            </span>
          )}
        </div>
      </div>
    </>
  );
}

export default Drawer;
