import React from "react";
import { useHistory } from "react-router-dom";
function AddressSelect({
  show,
  setshowAddress,
  address,
  addresses,
  setAddress,
  noEdit,
}) {
  const history = useHistory();
  console.log(addresses);
  const onChange = (e) => {
    const selected = e.target.value;
    setAddress(() => {
      return addresses.find((i) => i.id === +selected);
    });
    setshowAddress();
  };
  return (
    <>
      <div
        className={
          show
            ? "absolute bottom-0 z-20 flex flex-col w-full py-4 opacity-100 transition-opacity duration-500 bg-white "
            : "absolute bottom-0 z-20 flex flex-col w-full py-4 opacity-0 pointer-events-none  transition-opacity duration-500 bg-white "
        }
      >
        <div className="flex justify-between px-4 py-2 border-b ">
          <span className="font-bold">Choose Address</span>{" "}
          <span
            className="font-bold text-green-600 "
            onClick={() => {
              history.push({
                pathname: "/add_address",
                state: { from: "/place_order" },
              });
            }}
          >
            Add Address
          </span>
        </div>
        <div className="flex flex-col px-4 overflow-auto divide-y-2 divide-gray-200 h-80">
          <div className="py-4" key={address.id}>
            <div className="flex items-center ">
              <div>
                <input
                  type="radio"
                  value={address.id}
                  name="address"
                  checked={true}
                  onChange={onChange}
                />
              </div>
              <div className="ml-4">
                <div className="font-bold">{address.house_no}</div>
                <div>
                  {address.address_detail},{address.delivery_area},
                  {address.city},{address.pincode}
                </div>
              </div>
            </div>
            <div className="flex justify-end">
              {!noEdit && (
                <span
                  className="pl-10 font-bold text-green-400"
                  onClick={() =>
                    history.push({
                      pathname: "/edit_address",
                      state: { address: address, from: "/place_order" },
                    })
                  }
                >
                  Edit
                </span>
              )}
              <span
                className="pl-4 font-bold text-green-400"
                onClick={() => {
                  setshowAddress();
                }}
              >
                Select
              </span>
            </div>
          </div>
          {addresses.map((i) => {
            if (i.id !== address.id) {
              return (
                <div className="py-4" key={i.id}>
                  <div className="flex items-center ">
                    <div>
                      <input
                        type="radio"
                        name="address"
                        value={i.id}
                        onChange={onChange}
                      />
                    </div>
                    <div className="ml-4">
                      <div className="font-bold">{i.house_no}</div>
                      <div>
                        {i.address_detail},{i.delivery_area},{i.city},
                        {i.pincode}
                      </div>
                    </div>
                  </div>
                  <div className="flex justify-end">
                    {!noEdit && (
                      <span
                        className="pl-10 font-bold text-green-400"
                        onClick={() =>
                          history.push({
                            pathname: "/edit_address",
                            state: { address: address, from: "/place_order" },
                          })
                        }
                      >
                        Edit
                      </span>
                    )}
                    <span
                      className="pl-4 font-bold text-green-400"
                      onClick={() => {
                        setAddress(i);
                        setshowAddress();
                      }}
                    >
                      Select
                    </span>
                  </div>
                </div>
              );
            }
            return <></>;
          })}
        </div>
      </div>
    </>
  );
}

export default AddressSelect;
