import * as actionTypes from "./actionTypes";
import Axios from "axios";
Axios.defaults.withCredentials = true;

export const addToCart = (item) => {
  return (dispatch) => {
    dispatch({
      type: actionTypes.ADD_TO_CART,
      item: item,
      loading: false,
    });
  };
};

export const removeFromCart = (id) => {
  return {
    type: actionTypes.REMOVE_FROM_CART,
    id: id,
  };
};

export const incrementProduct = (id, sku_id) => {
  return {
    type: actionTypes.INCREMENT_PRODUCT,
    id,
    sku_id,
  };
};

export const decrementProduct = (id, sku_id) => {
  return {
    type: actionTypes.DECREMENT_PRODUCT,
    id,
    sku_id,
  };
};
const setCart = (cart) => {
  return {
    type: actionTypes.SET_CART,
    cart: cart,
  };
};

export const loading = (cart) => {
  return {
    type: actionTypes.CART_LOADING,
  };
};

export const validatedCart = (data) => {
  return {
    type: actionTypes.VALIDATED_CART,
    data,
  };
};

export const getCart = () => {
  return async (dispatch, getState) => {
    dispatch(loading());

    try {
      const { data } = await Axios.get(
        "https://frozen-reef-15026.herokuapp.com/api/cart"
      );
      dispatch(setCart(data));
    } catch (err) {
      dispatch(setCart([]));
    }
  };
};

export const clearCart = () => {
  return { type: actionTypes.CLEAR_CART };
};

export const initCart = () => {
  return async (dispatch, getState) => {
    dispatch(loading());
    try {
      const { data } = await Axios.get(
        "https://frozen-reef-15026.herokuapp.com/api/cart"
      );
      dispatch(setCart(data));
    } catch (err) {
      dispatch(setCart([]));
    }
  };
};
