import * as actionTypes from "../actions/actionTypes";

export const updateObject = (oldObject, updatedProperties) => {
  return {
    ...oldObject,
    ...updatedProperties,
  };
};
const initialState = {
  products: null,
  loading: false,
  page: 0,
  query: "",
  hasMore: false,
  brand: [],
  brand_filters: [],
  sort: "popularity",
};

const fetchProductStart = (state, action) => {
  return updateObject(state, { loading: true });
};

const setBrand = (state, action) => {
  let brand = [];
  brand = action.payload.brand.map((i) => {
    return { value: i.brand_id, label: i.brand.name };
  });
  return updateObject(state, { brand: brand });
};

const resetProducts = (state, action) => {
  return updateObject(state, { products: [] });
};
const resetPage = (state, action) => {
  return updateObject(state, { page: 0 });
};
const setHasMore = (state, action) => {
  return updateObject(state, { hasMore: action.data });
};
const fetchMoreProducts = (state, action) => {
  return updateObject(state, {
    products: [...state.products, ...action.data],
    loading: false,
  });
};
const setMoreSearch = (state, action) => {
  return updateObject(state, {
    products: [...state.products, ...action.data],
    loading: false,
  });
};

const fetchProductsSuccess = (state, action) => {
  return updateObject(state, {
    products: action.products,
    loading: false,
  });
};

const fetchProductsFailed = (state, action) => {
  return updateObject(state, { loading: false });
};
const setQuery = (state, action) => {
  return updateObject(state, { products: [], query: action.query });
};
const incrementPage = (state, action) => {
  return updateObject(state, { page: state.page + 1 });
};
const setLoading = (state, action) => {
  return updateObject(state, { loading: action.data });
};
const setBrandFilter = (state, action) => {
  return updateObject(state, {
    brand_filters: [...action.data],
  });
};

const setSort = (state, action) => {
  return updateObject(state, {
    sort: action.data,
  });
};

const resetFilters = (state, action) => {
  return updateObject(state, { brand_filters: [] });
};
const reducer = (state = initialState, action) => {
  switch (action.type) {
    // case actionTypes.PURCHASE_INIT:
    //   return purchaseInit(state, action);
    case actionTypes.FETCH_PRODUCTS_START:
      return fetchProductStart(state, action);
    case actionTypes.FETCH_PRODUCTS_SUCCESS:
      return fetchProductsSuccess(state, action);
    case actionTypes.FETCH_PRODUCTS_FAILED:
      return fetchProductsFailed(state, action);
    case actionTypes.SET_MORE_PRODUCTS:
      return fetchMoreProducts(state, action);
    case actionTypes.RESET_PAGE:
      return resetPage(state, action);
    case actionTypes.SET_HAS_MORE:
      return setHasMore(state, action);
    case actionTypes.INCREMENT_PAGE:
      return incrementPage(state, action);
    case actionTypes.SET_LOADING:
      return setLoading(state, action);
    case actionTypes.RESET_PRODUCTS:
      return resetProducts(state, action);
    case actionTypes.SET_QUERY:
      return setQuery(state, action);
    case actionTypes.SET_MORE_SEARCH:
      return setMoreSearch(state, action);
    case actionTypes.SET_BRAND:
      return setBrand(state, action);
    case actionTypes.SET_BRAND_FILTER:
      return setBrandFilter(state, action);
    case actionTypes.RESET_FILTERS:
      return resetFilters(state, action);
    case actionTypes.SET_SORT:
      return setSort(state, action);
    default:
      return state;
  }
};

export default reducer;
