import React from "react";
import { useDispatch } from "react-redux";
import * as actions from "../store/actions/index";

import Modal from "./modal";
function useToggle(initialValue = false) {
  const [value, setValue] = React.useState(initialValue);
  const toggle = () => {
    setValue((v) => !v);
  };
  return [value, toggle];
}
const ClearCart = () => {
  const [isOn, toggleIsOn] = useToggle();

  const dispatch = useDispatch();

  const children = () => {
    return (
      <>
        {" "}
        <div
          className="absolute inset-0 bg-black opacity-25"
          onClick={toggleIsOn}
        ></div>
        <div className="absolute inset-0 z-10 h-32 m-auto bg-white shadow-md">
          <div className="px-4 py-4">Are you sure you want to clear ?</div>
          <div className="flex justify-end px-4 py-4 space-x-4">
            <span
              onClick={toggleIsOn}
              className="px-4 py-2 font-bold text-white bg-black rounded-full hover:text-black hover:bg-white"
            >
              Cancel
            </span>
            <span
              onClick={() => {
                toggleIsOn();
                dispatchClear();
              }}
              className="px-4 py-2 font-bold text-white bg-red-600 rounded-full hover:bg-red-400"
            >
              Clear
            </span>
          </div>
        </div>
      </>
    );
  };
  const dispatchClear = () => {
    dispatch(actions.clearCart());
    // setShow(false);
  };
  return (
    <div className="px-3 font-semibold" onClick={toggleIsOn}>
      Clear
      {isOn ? <Modal children={children()} isOn={isOn}></Modal> : null}
    </div>
  );
};
export default ClearCart;
