import React from "react";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import Axios from "axios";
import Spinner from "../utils/Spinner/spinner";
import { useHistory } from "react-router-dom";
import { CustomInput } from "../utils/CustomInput";
Axios.defaults.withCredentials = true;

function AddProductType(props) {
  const history = useHistory();

  let initialValues = {
    sku: {
      product_id: props.location.state.id,
      mrp: 5,
      discount: 4,
      sp: 5,
      sku: 5,
      stock: 5,
    },
  };
  const validationSchema = Yup.object().shape({
    sku: Yup.object().shape({
      mrp: Yup.number().required("Enter Mrp of the Product"),
      discount: Yup.number(),
      sp: Yup.number().required("Enter the Selling Price of the product"),
      sku: Yup.string().required("Enter the SKU of the product"),
      stock: Yup.number().required("Enter the Stock of the product"),
    }),
  });

  async function _handleSubmit(values, actions) {
    console.log(values);
    try {
      actions.setSubmitting(true);
      const { data } = await Axios.post(
        `https://frozen-reef-15026.herokuapp.com/api/sku/`,
        values
      );
      actions.setSubmitting(false);
      actions.resetForm();
      history.push(`/product/${data.product_id}`);
    } catch (err) {
      console.log(JSON.stringify(err.response, 2, null));
      if (err.response.status === 409) {
        actions.setStatus({
          error:
            err.response.data.message + ":" + err.response.data.data.constraint,
        });
      } else if (err.response.status === 422) {
        actions.setStatus({ error: err.response.data.data });
      } else {
        actions.setStatus({ error: "Server Error" });
      }
      actions.setSubmitting(false);
    }
  }
  // const CustomInput = ({ label, onChange, value, ...props }) => {
  //   const [field, meta] = useField(props);
  //   return (
  //     <>
  //       <label className="text-gray-600" htmlFor={props.name}>
  //         {label}
  //       </label>
  //       <input
  //         {...field}
  //         onChange={onChange}
  //         value={value}
  //         className="w-full px-3 py-2 text-sm leading-tight text-gray-700 border rounded shadow appearance-none focus:outline-none focus:shadow-outline"
  //       />
  //       {meta.touched && meta.error ? (
  //         <div className="py-1 pl-4 text-white bg-red-400 ">{meta.error}</div>
  //       ) : null}
  //     </>
  //   );
  // };
  const parseAndHandleChange = (value, setFieldValue, id) => {
    console.log(isNaN(value));
    console.log(value);
    if (isNaN(value)) {
      return;
    }
    if (value === "") {
      setFieldValue(id, 0);
      return;
    }
    const parsed = parseFloat(value, 10);
    console.log(parsed);
    setFieldValue(id, parsed);
  };
  return (
    <div>
      <div className="flex flex-col h-screen ">
        <div className="flex py-2 text-white bg-red-500 ">
          <div className="flex items-center justify-center">
            <div className="px-2 " onClick={() => history.goBack()}>
              <svg
                fill="currentColor"
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                viewBox="0 0 24 24"
              >
                <path d="M16.67 0l2.83 2.829-9.339 9.175 9.339 9.167-2.83 2.829-12.17-11.996z" />
              </svg>
            </div>
            <div className="font-bold">Add Product Sku</div>
          </div>
        </div>
        <div className="flex flex-col flex-1 pt-4 mx-8 ">
          <Formik
            initialValues={initialValues}
            validationSchema={validationSchema}
            onSubmit={(values, actions) => _handleSubmit(values, actions)}
          >
            {({
              values,
              handleChange,
              setFieldValue,
              status,
              isSubmitting,
              validateForm,
              handleSubmit,
            }) => {
              return (
                <Form className="flex flex-col flex-1 ">
                  <div className="flex flex-col justify-between flex-1 ">
                    <div className="flex flex-col flex-1 pb-10 mx-4 space-y-2 ">
                      <CustomInput
                        label="Product Sku"
                        name="sku.sku"
                        type="text"
                        onChange={handleChange}
                        value={values.sku.sku}
                        placeholder="Product Sku"
                      ></CustomInput>
                      <CustomInput
                        label="Product Mrp"
                        name="sku.mrp"
                        type="number"
                        onChange={handleChange}
                        value={values.sku.mrp}
                        placeholder="Product Mrp"
                      ></CustomInput>
                      <CustomInput
                        label="Product Discount"
                        name="sku.discount"
                        type="number"
                        onChange={(event) =>
                          parseAndHandleChange(
                            event.target.value,
                            setFieldValue,
                            "sku.discount"
                          )
                        }
                        value={values.sku.discount}
                        placeholder="Discount"
                      ></CustomInput>
                      <CustomInput
                        label="Product Selling Price"
                        name="sku.sp"
                        type="number"
                        onChange={(event) =>
                          parseAndHandleChange(
                            event.target.value,
                            setFieldValue,
                            "sku.sp"
                          )
                        }
                        value={values.sku.sp}
                        placeholder="Selling Price"
                      ></CustomInput>
                      <CustomInput
                        label="Product Stock"
                        name="sku.stock"
                        type="number"
                        onChange={(event) =>
                          parseAndHandleChange(
                            event.target.value,
                            setFieldValue,
                            "sku.stock"
                          )
                        }
                        value={values.sku.stock}
                        placeholder="Product Stock"
                      ></CustomInput>
                    </div>

                    {status && status.error ? (
                      <div className="px-2 py-1 text-white bg-red-400">
                        {status.error}
                      </div>
                    ) : null}
                    <div className="flex justify-end pb-16">
                      {!values.sku.id ? (
                        isSubmitting ? (
                          <div className="px-8 py-4 mr-2 text-white bg-gray-300 rounded ">
                            {" "}
                            <Spinner width={15} height={15}></Spinner>
                          </div>
                        ) : (
                          <button
                            type="submit"
                            onClick={() =>
                              validateForm().then((e) => {
                                console.log(e);
                                if (!e) {
                                  return handleSubmit(values);
                                }
                                console.log(values);
                                return;
                              })
                            }
                            className="px-4 py-4 mr-2 text-white bg-green-600 rounded "
                          >
                            Next
                          </button>
                        )
                      ) : null}
                    </div>
                  </div>
                </Form>
              );
            }}
          </Formik>
        </div>
      </div>
    </div>
  );
}

export default AddProductType;
