import { useField } from "formik";
import React from "react";

const LoginInput = ({label,...props}) => {
  const [field, meta] = useField(props);
  return (
    <div>
      <label
        htmlFor="phoneNumber"
        className="block pt-4 mx-12 text-sm font-medium text-gray-700"
      >
        {label}
      </label>
      <div className="relative mt-1 rounded-md ">
       {props.Symbol&& <div className="absolute inset-y-0 left-0 flex items-center pointer-events-none pl-14">
        <span>+91</span></div>}
        <input {...field} disabled={props.disabled}
          type={props.type}
          
          className="block pl-10 mx-12 border border-gray-300 rounded-md pr-7 focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
          placeholder={props.placeholder}

        ></input>
     {meta.touched&&meta.error?    <div className="absolute inset-y-0 right-0 flex items-center pr-20 pointer-events-none">
        <span><svg xmlns="http://www.w3.org/2000/svg" fill="none" className="w-8 h-8 text-red-800" viewBox="0 0 24 24" stroke="currentColor">
  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
</svg></span></div>:null}
     
      </div>{meta.touched && meta.error ? (
          <div className="py-2 pl-4 my-2 text-red-500 border border-red-600 ">{meta.error}</div>
        ) : null}
      

        
        
      
      
    </div>
  );
};

const Button=({text},...props)=>
  <button type="submit"   onClick={props.onClick} className='items-center block px-4 py-2 mx-auto mt-4 text-base leading-6 text-white transition duration-150 ease-in-out bg-purple-500 border border-transparent rounded-md shadow-sm focus:outline-none focus:ring-purple-300 hover:bg-purple-300 active:bg-purple-700 '>
      
        
        {text}</button>

export  {LoginInput,Button};
