import React from "react";
import { useHistory } from "react-router-dom";
export default function ({ product, sku }) {
  let history = useHistory();
  return (
    <div className="flex flex-col space-y-1 overflow-auto">
      <div className="flex px-2 pt-1 pb-2 mt-2 space-x-2 border-b border-gray-500 product-detail">
        <div className="w-1/3 image relative pb-0.5 ">
          <img
            onClick={() => history.push(`/product/${product.id}`)}
            src={sku.images[0].image}
            alt={product.name}
            className="absolute object-contain w-full h-full"
          />
        </div>
        <div className="flex flex-col w-3/4 space-y-1">
          <div className="flex flex-wrap flex-grow-0 font-semibold ">
            {product.name}
          </div>

          <div className="flex space-x-2 text-gray-900">
            <span>{sku.sp}</span>
          </div>

          {sku.discount > 0 ? (
            <div className="flex items-center initial-price">
              <span className=""> &#8377; </span>
              <span className="line-through text">{sku.mrp}</span>
              <div className="flex px-1 ml-1 space-x-1 text-white bg-red-400">
                <span>{sku.discount.toFixed(2)}%</span>
                <span>OFF</span>
              </div>{" "}
            </div>
          ) : null}

          <div className="flex items-center justify-between">
            <div className="flex items-center ml-4 font-extrabold">
              <span> &#8377; </span>
              <span>{sku.sp}</span>
            </div>{" "}
          </div>
        </div>
      </div>
    </div>
  );
}
