import * as actionTypes from "../actions/actionTypes";

export const updateObject = (oldObject, updatedProperties) => {
  return {
    ...oldObject,
    ...updatedProperties,
  };
};
const initialState = {
  orders: [],
  loading: null,
  purchased: false,
  orderId: null,
};

const purchaseInit = (state, action) => {
  return updateObject(state, { purchasing: true });
};
const purchaseReset = (state, action) => {
  return updateObject(state, { purchased: false });
};

const orderStart = (state, action) => {
  return updateObject(state, { loading: true });
};

const orderSuccess = (state, action) => {
  return updateObject(state, {
    loading: false,
    orderId: action.orderId,
    purchased: true,
  });
};

const orderFail = (state, action) => {
  return updateObject(state, { loading: false });
};

const fetchOrdersStart = (state, action) => {
  return updateObject(state, { loading: true });
};

const fetchOrdersSuccess = (state, action) => {
  console.log(action);
  return updateObject(state, {
    orders: action.orders,
    loading: false,
  });
};

const fetchOrdersFail = (state, action) => {
  return updateObject(state, { loading: false });
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.PURCHASE_INIT:
      return purchaseInit(state, action);
    case actionTypes.PURCHASE_RESET:
      return purchaseReset(state, action);
    case actionTypes.CREATE_ORDER_START:
      return orderStart(state, action);
    case actionTypes.CREATE_ORDER_SUCCESS:
      return orderSuccess(state, action);
    case actionTypes.CREATE_ORDER_FAILED:
      return orderFail(state, action);
    case actionTypes.FETCH_ORDERS_START:
      return fetchOrdersStart(state, action);
    case actionTypes.FETCH_ORDERS_SUCCESS:
      return fetchOrdersSuccess(state, action);
    case actionTypes.FETCH_ORDERS_FAIL:
      return fetchOrdersFail(state, action);
    default:
      return state;
  }
};

export default reducer;
