import React from "react";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import { CustomInput, CustomTextArea } from "../utils/CustomInput";
import { useSelector } from "react-redux";
import Axios, { CancelToken } from "axios";
import Select from "react-select";
import { useHistory } from "react-router-dom";
import Spinner from "../utils/Spinner/spinner";

import { useQuery } from "react-query";
Axios.defaults.withCredentials = true;

const AddProduct = (props) => {
  const { data, status } = useQuery(
    "brand-query",
    () => {
      const source = CancelToken.source();

      const promise = Axios.get(
        "https://frozen-reef-15026.herokuapp.com/api/allbrands",
        {
          cancelToken: source.token,
        }
      );

      // Cancel the request if React Query calls the `promise.cancel` method
      promise.cancel = () => {
        source.cancel("Query was cancelled by React Query");
      };

      return promise;
    },
    {
      refetchOnMount: false,
      refetchOnWindowFocus: false,
      cacheTime: 600000,
      staleTime: 60000,
    }
  );
  let history = useHistory();
  let product;
  if (props.location.state) {
    product = props.location.state.product;
  }
  const category = useSelector((state) => state.categoryBuilder.category);

  let initialValues;

  let tags = [];
  if (product) {
    console.log(product);
    initialValues = {
      product: {
        id: product.id,
        name: product.name,
        desc: product.desc,

        brand_id: product.brand_id,
        brandName: "",
        category_id: product.category_id,

        product_category_id: product.product_category_id,
      },
    };
  } else {
    initialValues = {
      product: {
        name: "",
        desc: "",

        brand_id: "",
        brandName: "",
        category_id: "",

        product_category_id: "",
      },
    };
  }
  const selectSubCat = (id) => {
    if (category.length !== 0) {
      const selectCategory = category.find((i) => i.id === +id);
      const subCat = selectCategory ? selectCategory.subCat : [];
      return subCat;
    } else return [];
  };
  let categoryOptions = [];
  let productCategoryOptions = [];
  category.forEach((i) => {
    i.subCat.forEach((i) =>
      productCategoryOptions.push({ name: i.name, id: i.id })
    );
    categoryOptions.push({ name: i.name, id: i.id });
  });
  const validationSchema = Yup.object().shape({
    product: Yup.object().shape(
      {
        name: Yup.string().required("Please enter a Name"),
        desc: Yup.string().required("Please Enter a desc"),

        category_id: Yup.number("Please select one").required(
          "Please select a category"
        ),
        product_category_id: Yup.number("Please select one").required(
          "Please select a product category"
        ),
        brand_id: Yup.mixed()
          .test(
            "Enter a Brand Name or Select a Brand",
            "Enter a name or Select a Brand",
            function (brand_id) {
              const { brandName } = this.parent;

              if (brand_id && brandName) {
                return false;
              }
              return true;
            }
          )
          .when(["brandName"], {
            is: (val) => {
              return !val;
            },
            then: Yup.number().required(" is required"),
          }),

        brandName: Yup.mixed()
          .test(
            "Enter a Brand Name or Select a Brand",
            "Enter a name or Select a Brand",
            function (brandName) {
              const { brand_id } = this.parent;
              if (brand_id && brandName) {
                return false;
              }
              return true;
            }
          )
          .when(["brand_id"], {
            is: (val) => {
              return !val;
            },
            then: Yup.string().required(" Brand Name is required"),
          }),
      },
      ["brand_id", "brandName"]
    ),
  });

  async function editProduct(values, actions) {
    let product;
    let brand = {
      name: "",
    };
    if (values.product.brandName) {
      brand.name = values.product.brandName;
      delete values.product.brand_id;
    }

    delete values.product.brandName;
    product = { ...values.product };
    product.brand = brand;

    try {
      actions.setSubmitting(true);
      const { data } = await Axios.patch(
        `https://frozen-reef-15026.herokuapp.com/api/product/${product.id}`,
        {
          product: product,
        }
      );
      actions.setSubmitting(false);
      actions.resetForm();
      history.push(`/product/${data.id}`);
    } catch (err) {
      console.log(JSON.stringify(err.response, 2, null));
      if (err.response.status === 409) {
        actions.setStatus({ error: err.response.data.details });
      } else if (err.response.status === 422) {
        actions.setStatus({ error: err.response.message });
      } else {
        actions.setStatus({ error: "Server Error" });
      }
      actions.setSubmitting(false);
    }
  }

  async function _handleSubmit(values, actions) {
    console.log(values);

    let product;
    let brand = {
      name: "",
    };
    if (values.product.brandName) {
      brand.name = values.product.brandName;
      delete values.product.brand_id;
    }
    product = { ...values.product };
    delete product.brandName;
    product.brand = brand;

    try {
      actions.setSubmitting(true);
      const { data } = await Axios.post(
        `https://frozen-reef-15026.herokuapp.com/api/product/`,
        {
          product: product,
        }
      );
      actions.setSubmitting(false);
      actions.resetForm();
      history.push(`/product/${data.id}`);
    } catch (err) {
      console.log(JSON.stringify(err.response, 2, null));
      if (err.response.status === 409) {
        actions.setStatus({
          error:
            err.response.data.message + ":" + err.response.data.data.constraint,
        });
      } else if (err.response.status === 422) {
        actions.setStatus({ error: err.response.data.message });
      } else {
        actions.setStatus({ error: "Server Error" });
      }
      actions.setSubmitting(false);
    }
  }
  // const CustomInput = ({ label, ...props }) => {
  //   const [field, meta] = useField(props);
  //   return (
  //     <>
  //       <label className="text-gray-600" htmlFor={props.name}>
  //         {label}
  //       </label>
  //       <input
  //         {...field}
  //         className="w-full px-3 py-2 text-sm leading-tight text-gray-700 border rounded shadow appearance-none focus:outline-none focus:shadow-outline"
  //       />
  //       {meta.touched && meta.error ? (
  //         <div className="py-1 pl-4 text-white bg-red-400 ">{meta.error}</div>
  //       ) : null}
  //     </>
  //   );
  // };

  // const CustomTextArea = ({ label, ...props }) => {
  //   const [field, meta] = useField(props);
  //   return (
  //     <div className="flex flex-col flex-shrink-0">
  //       {" "}
  //       <label className="text-gray-600" htmlFor={props.name}>
  //         {label}
  //       </label>
  //       <textarea
  //         {...field}
  //         className="w-full h-32 px-3 py-2 text-sm leading-tight text-gray-700 border rounded shadow appearance-none focus:outline-none focus:shadow-outline"
  //       />
  //       {meta.touched && meta.error ? (
  //         <div className="py-1 pl-4 text-white bg-red-400 ">{meta.error}</div>
  //       ) : null}
  //     </div>
  //   );
  // };

  const parseAndHandleChange = (value, setFieldValue, id) => {
    const parsed = parseInt(value, 10);
    if (isNaN(parsed)) {
      console.log(parsed);
      return;
    }
    setFieldValue(id, parsed);
  };

  const render = () => {
    if (status === "loading") {
      return <div></div>;
    } else {
      console.log(data);
      return (
        <div>
          <div className="flex flex-col h-screen ">
            <div className="flex py-2 text-white bg-red-500 ">
              <div className="flex items-center justify-center">
                <div className="px-2 " onClick={() => history.goBack()}>
                  <svg
                    fill="currentColor"
                    xmlns="http://www.w3.org/2000/svg"
                    width="16"
                    height="16"
                    viewBox="0 0 24 24"
                  >
                    <path d="M16.67 0l2.83 2.829-9.339 9.175 9.339 9.167-2.83 2.829-12.17-11.996z" />
                  </svg>
                </div>
                <div className="font-bold">
                  {props.location.state
                    ? props.location.state.page
                    : "Add Product"}
                </div>
              </div>
            </div>
            <div className="flex flex-col flex-1 pt-4 mx-8 ">
              <Formik
                initialValues={initialValues}
                validationSchema={validationSchema}
                onSubmit={(values, actions) => {
                  if (values.product.id) {
                    editProduct(values, actions);
                  } else {
                    _handleSubmit(values, actions);
                  }
                }}
              >
                {({
                  values,
                  handleSubmit,
                  handleChange,
                  setFieldValue,
                  status,
                  errors,
                  isSubmitting,
                  validateForm,
                  touched,
                }) => {
                  return (
                    <Form className="flex flex-col flex-1 ">
                      <div className="flex flex-col justify-between flex-1 ">
                        <div className="flex flex-col flex-1 pb-10 mx-4 space-y-2 ">
                          <CustomInput
                            label="Product Name"
                            name="product.name"
                            type="text"
                            onChange={handleChange}
                            symbol={true}
                            value={values.product.name}
                            placeholder="Product Name"
                          ></CustomInput>
                          <CustomTextArea
                            label="Product desc"
                            name="product.desc"
                            type="text"
                            onChange={handleChange}
                            value={values.product.desc}
                            placeholder="desc"
                          ></CustomTextArea>

                          <CustomInput
                            label="Product Brand"
                            name="product.brandName"
                            type="text"
                            onChange={handleChange}
                            value={values.product.brandName}
                            placeholder="Brand"
                          ></CustomInput>

                          <Select
                            value={data.data.find(
                              (i) => i.value === values.product.brand_id
                            )}
                            onChange={(opt, e) => {
                              if (e.action === "clear") {
                                setFieldValue("product.brand_id", "");
                              }
                              if (opt) {
                                setFieldValue("product.brand_id", opt.value);
                              }
                            }}
                            name="product.brand_id"
                            isSearchable={true}
                            isClearable={true}
                            options={data.data}
                          ></Select>
                          {errors.product &&
                          touched.product &&
                          touched.product.brand_id &&
                          errors.product.brand_id ? (
                            <div className="px-2 py-1 text-white bg-red-400">
                              {errors.product.brand_id}
                            </div>
                          ) : null}
                          <div className="flex flex-col pt-2 space-y-2">
                            <div className="font-bold"> Category</div>
                            <select
                              onChange={(event) =>
                                parseAndHandleChange(
                                  event.target.value,
                                  setFieldValue,
                                  "product.category_id"
                                )
                              }
                              placeholder=""
                              name="product.category_id"
                              value={values.product.category_id}
                            >
                              <option value="" key="">
                                Select Category
                              </option>
                              {categoryOptions.map((i) => (
                                <option key={i.id} value={i.id}>
                                  {i.name}
                                </option>
                              ))}
                            </select>
                            {errors.product &&
                            touched.product &&
                            touched.product.category_id &&
                            errors.product.category_id ? (
                              <div className="px-2 py-1 text-white bg-red-400">
                                {errors.product.category_id}
                              </div>
                            ) : null}
                          </div>
                          <div className="flex flex-col pt-2 space-y-2">
                            <div className="font-bold"> Product Category</div>
                            <select
                              onChange={(event) =>
                                parseAndHandleChange(
                                  event.target.value,
                                  setFieldValue,
                                  "product.product_category_id"
                                )
                              }
                              placeholder=""
                              name="product.product_category_id"
                              value={values.product.product_category_id}
                            >
                              <option>Select Product Category</option>
                              {selectSubCat(values.product.category_id).map(
                                (i) => (
                                  <option key={i.id} value={i.id}>
                                    {i.name}
                                  </option>
                                )
                              )}
                            </select>
                            {errors.product &&
                            touched.product &&
                            touched.product.product_category_id &&
                            errors.product.product_category_id ? (
                              <div className="px-2 py-1 text-white bg-red-400">
                                {errors.product.product_category_id}
                              </div>
                            ) : null}
                          </div>
                        </div>
                        {status && status.error ? (
                          <div className="px-2 py-1 text-white bg-red-400">
                            {status.error}
                          </div>
                        ) : null}
                        <div className="flex justify-end pb-16">
                          {values.product.id ? (
                            isSubmitting ? (
                              <button
                                disabled
                                className="px-8 py-4 mr-2 text-white bg-gray-300 rounded "
                              >
                                <Spinner width={15} height={15}></Spinner>
                              </button>
                            ) : (
                              <button
                                type="submit"
                                onClick={() =>
                                  validateForm().then((e) => {
                                    if (!e) {
                                      return editProduct(values);
                                    }
                                    return;
                                  })
                                }
                                className="px-4 py-2 mr-2 text-white bg-green-600 rounded "
                              >
                                Edit
                              </button>
                            )
                          ) : null}
                          {!values.product.id ? (
                            isSubmitting ? (
                              <div className="px-8 py-4 mr-2 text-white bg-gray-300 rounded ">
                                {" "}
                                <Spinner width={15} height={15}></Spinner>
                              </div>
                            ) : (
                              <button
                                type="submit"
                                onClick={() =>
                                  validateForm().then((e) => {
                                    if (!e) {
                                      return _handleSubmit(values);
                                    }
                                    return;
                                  })
                                }
                                className="px-4 py-4 mr-2 text-white bg-green-600 rounded "
                              >
                                Next
                              </button>
                            )
                          ) : null}
                        </div>
                      </div>
                    </Form>
                  );
                }}
              </Formik>
            </div>
          </div>
        </div>
      );
    }
  };

  return render();
};

export default AddProduct;
