import React, { useState, useEffect } from "react";

import { useSelector, useDispatch } from "react-redux";
import * as actions from "../store/actions/index";
import { Redirect, useHistory } from "react-router-dom";
import Select from "react-select";
import BackDrop from "../components/BackDrop";
import AddressSelect from "../components/addressSelect";

import { Formik, Form } from "formik";
import * as Yup from "yup";
import Spinner from "../utils/Spinner/spinner";
import Axios from "axios";
function PlaceOrder() {
  const store = [
    {
      house_no: "Jyothi Kurnool",
      id: 1,
      address: "Park Road",
      delivery_area: "Zilla",
      city: "Kurnool",
      pincode: 33232,
    },
  ];
  let date = new Date();
  let today = date.toLocaleDateString();
  let tom = new Date(date);
  tom.setDate(tom.getDate() + 1);

  let tomorrow = tom.toLocaleDateString();
  const [showAddress, setshowAddress] = useState(false);
  const cart = useSelector((state) => state.cartBuilder.cart);
  const addresses = useSelector((state) => state.authBuilder.address);
  const loading = useSelector((state) => state.authBuilder.loading);
  const orderLoading = useSelector((state) => state.orderBuilder.loading);
  const orderId = useSelector((state) => state.orderBuilder.orderId);
  const purchased = useSelector((state) => state.orderBuilder.purchased);
  const user_id = useSelector((state) => state.authBuilder.userId);
  console.log(addresses);
  const backDrop = () => {
    if (showAddress) {
      return <BackDrop onClick={() => setshowAddress(false)}></BackDrop>;
    } else {
      return null;
    }
  };

  function calc(cart, property) {
    if (cart) {
      if (property === "mrp") {
        let amount = 0;
        cart.map((i) => (amount += i.skuId.mrp * i.quantity));
        return amount.toFixed(2);
      } else if (property === "sp") {
        let amount = 0;
        cart.map((i) => (amount += i.skuId.sp * i.quantity));
        return amount.toFixed(2);
      }
    }
  }
  const total_price = calc(cart, "mrp");
  const total_selling_price = calc(cart, "sp");
  const discount = (total_price - total_selling_price).toFixed(2);
  const [address, setAddress] = useState();
  let total_bill = (parseFloat(total_selling_price) + 45.0).toFixed(2);

  console.log({ total_bill });

  const dispatch = useDispatch();
  const history = useHistory();

  useEffect(() => {
    if (addresses && addresses.length) {
      setAddress(addresses[0]);
    }
  }, [addresses]);

  const [step, setStep] = useState(0);

  const validationSchema = [
    Yup.object({
      delivery_slot: Yup.string().required("Please select a delivery Slot"),

      delivery_time: Yup.string().required("Please select Delivery Time"),
    }),
    Yup.object({
      payment_type: Yup.string().required("Please select a  Payment Type "),
    }),
  ];
  console.log(address);

  const render = () => {
    if (cart && !cart.length) {
      return <Redirect to="/"></Redirect>;
    } else if (loading) {
      return (
        <div className="flex flex-col items-center w-full h-full">
          <div className="flex items-center justify-center flex-1">
            <Spinner></Spinner>
          </div>
        </div>
      );
    } else if (!loading && addresses && !addresses.length) {
      return (
        <Redirect
          to={{
            pathname: "/add_address",
            state: { from: "/place_order" },
          }}
        ></Redirect>
      );
    } else if (cart && cart.length && addresses && address) {
      if (step === 0 || step === 1) {
        return (
          <div>
            <div className="relative flex flex-col h-screen ">
              {backDrop()}

              <div className="flex items-center text-white bg-red-500 ">
                <div className="flex items-center justify-center py-2">
                  <div className="px-2 " onClick={() => history.goBack()}>
                    <svg
                      fill="currentColor"
                      xmlns="http://www.w3.org/2000/svg"
                      width="16"
                      height="16"
                      viewBox="0 0 24 24"
                    >
                      <path d="M16.67 0l2.83 2.829-9.339 9.175 9.339 9.167-2.83 2.829-12.17-11.996z" />
                    </svg>
                  </div>

                  <div className="pl-4 ">Place Order</div>
                </div>
              </div>

              <Formik
                initialValues={{
                  delivery_slot: "",
                  delivery_time: "",
                  payment_type: "",
                  delivery_instructions: "",
                }}
                validationSchema={validationSchema[step]}
                onSubmit={async (values, { setSubmitting, setErrors }) => {
                  if (step === 1) {
                    const order = {
                      delivery_instructions: values.delivery_instructions,
                      delivery_date:
                        values.delivery_slot === "Today" ? today : tomorrow,
                      delivery_time: values.delivery_time,
                      payment_type: values.payment_type,
                      delivery_area: address.delivery_area,
                      user_id: user_id,
                      delivery_charges: 45.0,
                      order_items: cart,
                      address: address.address,
                      city: address.city,
                      pincode: address.pincode,
                    };
                    setSubmitting(true);
                    try {
                      const { data } = await Axios.post(
                        "https://frozen-reef-15026.herokuapp.com/api/order",
                        order
                      );

                      setSubmitting(false);
                      history.replace({
                        pathname: "/order_confirm",
                        state: { order_id: data.id },
                      });
                    } catch (err) {
                      setSubmitting(false);
                      setErrors({ err: err.response.message });
                    }
                  }
                }}
              >
                {({
                  values,
                  handleSubmit,
                  handleChange,
                  setFieldValue,
                  errors,
                  touched,
                  isSubmitting,
                  validateForm,
                }) => (
                  <Form className="flex flex-col flex-1 overflow-y-auto">
                    {step === 0 ? (
                      <div className="flex flex-col flex-1 overflow-y-auto">
                        <div className="flex flex-col flex-1 overflow-y-auto">
                          <div className="flex items-center justify-end px-4 py-1 bg-gray-140">
                            <span className="pr-4 font-extrabold">
                              I will pickup from store
                            </span>
                            <input
                              type="checkbox"
                              checked={step === 3}
                              onChange={() => {
                                if (step === 0) {
                                  setStep(3);
                                } else {
                                  setStep(0);
                                }
                              }}
                            />
                          </div>
                          {addresses[0] && address && addresses.length > 0 && (
                            <AddressSelect
                              show={showAddress}
                              addresses={addresses}
                              address={address}
                              setshowAddress={() => setshowAddress(false)}
                              setAddress={(e) => setAddress(e)}
                            ></AddressSelect>
                          )}
                          <div className="h-4"></div>
                          <div className="flex flex-col flex-1 overflow-y-auto ">
                            <div className="flex flex-col address">
                              <div className="px-4 py-1 font-bold bg-gray-140">
                                Select Delivery Address *
                              </div>
                              <div className="px-4 pt-3 bg-white">
                                <div className="flex ">
                                  <div>
                                    <svg
                                      xmlns="http://www.w3.org/2000/svg"
                                      width="24"
                                      height="24"
                                      viewBox="0 0 24 24"
                                    >
                                      <path d="M12 0c-4.198 0-8 3.403-8 7.602 0 4.198 3.469 9.21 8 16.398 4.531-7.188 8-12.2 8-16.398 0-4.199-3.801-7.602-8-7.602zm0 11c-1.657 0-3-1.343-3-3s1.343-3 3-3 3 1.343 3 3-1.343 3-3 3z" />
                                    </svg>
                                  </div>
                                  <div className="w-full pl-3">
                                    <div className="font-bold house_no">
                                      {address.house_no}
                                    </div>
                                    <div>{address.address}</div>
                                    <div>
                                      {address.delivery_area},{address.city},
                                      {address.pincode}
                                    </div>
                                  </div>
                                </div>
                                <div className="flex justify-end">
                                  <span
                                    className="font-bold text-green-700 "
                                    onClick={() => setshowAddress(true)}
                                  >
                                    Change
                                  </span>
                                </div>
                              </div>
                            </div>
                            <div className="p-3"></div>
                            <div className="w-full p-4 bg-gray-140"></div>
                            <div>
                              <div className="pt-4 slot">
                                <div className="px-4 bg-gray-140">
                                  <div className="pt-4 font-bold ">
                                    Delivery Slot *
                                  </div>
                                  <div className="flex items-center pt-3">
                                    <div className="w-1/2">
                                      <input
                                        type="radio"
                                        value="Today"
                                        name="delivery_slot"
                                        checked={
                                          values.delivery_slot === "Today"
                                        }
                                        onChange={() =>
                                          setFieldValue(
                                            "delivery_slot",
                                            "Today"
                                          )
                                        }
                                      />
                                      <span className="pl-2 font-bold ">
                                        {" "}
                                        Today
                                      </span>
                                    </div>
                                    <div className="w-1/2">
                                      <input
                                        type="radio"
                                        checked={
                                          values.delivery_slot === "Tomorrow"
                                        }
                                        onChange={() =>
                                          setFieldValue(
                                            "delivery_slot",
                                            "Tomorrow"
                                          )
                                        }
                                        value="Tomorrow"
                                        name="delivery_slot"
                                      />
                                      <span className="pl-2 font-bold ">
                                        {" "}
                                        Tomorrow
                                      </span>
                                    </div>
                                  </div>
                                </div>
                                <div className="h-5 bg-gray-140"></div>
                              </div>
                              {errors.delivery_slot && touched.delivery_slot ? (
                                <div className="px-2 py-1 text-white bg-red-400">
                                  {errors.delivery_slot}
                                </div>
                              ) : null}
                              <div className="mt-4 bg-gray-140 time">
                                <div className="px-4 pt-4 font-bold">
                                  Delivery Time *
                                </div>
                                <Select
                                  className="pt-4 "
                                  onChange={(opt, e) => {
                                    if (e.action === "clear") {
                                      setFieldValue("delivery_time", "");
                                    }
                                    if (opt) {
                                      setFieldValue("delivery_time", opt.value);
                                    }
                                  }}
                                  name="delivery_time"
                                  isSearchable={true}
                                  isClearable={true}
                                  openMenuOnClick={true}
                                  minMenuHeight={500}
                                  menuPlacement="top"
                                  options={[
                                    {
                                      label: "9:00 AM - 7:30 PM ",
                                      value: "9:00 AM - 7:30 PM",
                                    },
                                  ]}
                                  placeholder="Select available time slots"
                                ></Select>
                                <div className="h-4"></div>
                                {errors.delivery_time &&
                                touched.delivery_time ? (
                                  <div className="px-2 py-1 text-white bg-red-400">
                                    {errors.delivery_time}
                                  </div>
                                ) : null}
                              </div>
                            </div>
                          </div>
                        </div>

                        <div className="mb-14 ">
                          <div className="flex items-center py-4 bg-gray-300 ">
                            <span className="w-16 h-0 border border-green-500"></span>
                            <span className="px-2 font-bold border border-green-500 rounded-full">
                              Delivery
                            </span>
                            <span className="w-16 h-0 border border-white"></span>
                            <span className="px-1 font-bold border border-white rounded-full ">
                              Billing
                            </span>
                          </div>
                          <div className="flex justify-end py-2">
                            <button
                              type="submit"
                              onClick={() =>
                                validateForm().then((e) => {
                                  console.log(e);
                                  if (!Object.keys(e).length) {
                                    return setStep(1);
                                  }
                                  return;
                                })
                              }
                              className="px-4 py-2 mr-2 text-white bg-green-600 rounded "
                            >
                              Place Order
                            </button>
                          </div>
                        </div>
                      </div>
                    ) : (
                      <div className="flex flex-col flex-1 ">
                        <div className="flex-1 ">
                          {" "}
                          <div className="flex flex-col space-y-2">
                            <div className="font-bold ">Payment Option</div>
                            <div className="flex justify-evenly">
                              <div>
                                <input
                                  className="mr-2"
                                  type="radio"
                                  value="Cash on Delivery"
                                  checked={
                                    values.payment_type === "Cash on Delivery"
                                  }
                                  name="payment_type"
                                  onChange={() =>
                                    setFieldValue(
                                      "payment_type",
                                      "Cash on Delivery"
                                    )
                                  }
                                />

                                <label className="font-semibold">
                                  Cash on Delivery
                                </label>
                              </div>
                              <div>
                                <input
                                  className="mr-2"
                                  type="radio"
                                  value="GPay on Delivery"
                                  checked={
                                    values.payment_type === "GPay on Delivery"
                                  }
                                  onChange={() =>
                                    setFieldValue(
                                      "payment_type",
                                      "GPay on Delivery"
                                    )
                                  }
                                  name="payment_type"
                                />
                                <label
                                  htmlFor="Tomorrow"
                                  className="font-semibold"
                                >
                                  GPay on Delivery
                                </label>
                              </div>
                            </div>
                          </div>
                          {errors.payment_type && touched.payment_type ? (
                            <div className="px-2 py-1 text-white bg-red-400">
                              {errors.payment_type}
                            </div>
                          ) : null}
                          <div className="py-2">
                            <label
                              htmlFor="
                      Delivery Instructions"
                              className="block py-2 font-bold"
                            >
                              Delivery Instructions
                            </label>
                            <textarea
                              className="w-full h-32 py-2 border border-black"
                              value={values.delivery_instructions}
                              onChange={handleChange}
                              name="delivery_instructions"
                            ></textarea>
                          </div>
                          <div className="flex flex-col px-2 py-3 space-y-2 bg-gray-140">
                            <div className="flex justify-between text-gray-500">
                              <span>MRP Total</span>
                              <span className="">
                                <span>&#8377;</span>
                                <span className="pl-2 line-through">
                                  {total_price}
                                </span>
                              </span>
                            </div>
                            <div className="flex justify-between font-bold ">
                              <span>Sub Total</span>
                              <span className="">
                                <span>&#8377;</span>
                                <span className="ml-2">
                                  {total_selling_price}
                                </span>
                              </span>
                            </div>
                            <hr />
                            <div className="flex justify-between font-bold ">
                              <span>Delivery charge </span>
                              <span className="">
                                <span>&#8377;</span>
                                <span className="ml-2">45.00</span>
                              </span>
                            </div>
                            <div className="flex justify-between py-1 font-bold ">
                              <span>Total Bill</span>
                              <span className="">
                                <span>&#8377;</span>
                                <span className="ml-2">{total_bill}</span>
                              </span>
                            </div>
                            <div className="flex items-center justify-center text-white ">
                              <span className="px-1 font-bold bg-red-300 border">
                                {discount > 0
                                  ? `Order Now and Save ${discount}`
                                  : "Order Now"}
                              </span>
                            </div>
                          </div>
                        </div>
                        {errors.err && errors ? (
                          <div className="py-2 pl-4 my-2 text-red-500 border border-red-600 ">
                            {errors.err}
                          </div>
                        ) : null}
                        <div className="mb-14 ">
                          <div className="flex items-center py-4 bg-gray-300 ">
                            <span className="w-16 h-0 border border-green-500"></span>
                            <span className="px-2 font-bold border border-green-500 rounded-full">
                              Delivery
                            </span>
                            <span className="w-16 h-0 border border-green-500"></span>
                            <span className="px-1 font-bold border border-green-500 rounded-full ">
                              Billing
                            </span>
                          </div>
                          <div className="flex justify-end py-2">
                            {!isSubmitting ? (
                              <button
                                type="submit"
                                className="px-4 py-2 mr-2 text-white bg-green-600 rounded "
                              >
                                Place Order
                              </button>
                            ) : (
                              <button className="inline-flex items-center px-3 py-2 mx-20 mt-4 text-base leading-6 text-white transition duration-150 ease-in-out bg-indigo-500 border border-transparent rounded-md shadow-sm focus:outline-none focus:ring-indigo-300 hover:bg-indigo-300 active:bg-indigo-700 ">
                                <svg
                                  class="animate-spin -ml-1 mr-3 h-5 w-5 text-white"
                                  xmlns="http://www.w3.org/2000/svg"
                                  fill="none"
                                  viewBox="0 0 24 24"
                                >
                                  <circle
                                    class="opacity-25"
                                    cx="12"
                                    cy="12"
                                    r="10"
                                    stroke="currentColor"
                                    stroke-width="4"
                                  ></circle>
                                  <path
                                    class="opacity-75"
                                    fill="currentColor"
                                    d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                                  ></path>
                                </svg>
                                Processing
                              </button>
                            )}
                          </div>
                        </div>
                      </div>
                    )}
                  </Form>
                )}
              </Formik>
            </div>
          </div>
        );
      } else if (step === 3 || step === 4) {
        return (
          <div>
            <div className="relative flex flex-col h-screen ">
              <div className="flex items-center text-white bg-red-500 ">
                <div className="flex items-center justify-center py-2">
                  <div className="px-2 " onClick={() => history.goBack()}>
                    <svg
                      fill="currentColor"
                      xmlns="http://www.w3.org/2000/svg"
                      width="16"
                      height="16"
                      viewBox="0 0 24 24"
                    >
                      <path d="M16.67 0l2.83 2.829-9.339 9.175 9.339 9.167-2.83 2.829-12.17-11.996z" />
                    </svg>
                  </div>

                  <div className="pl-4 ">Place Order</div>
                </div>
              </div>

              <Formik
                initialValues={{
                  delivery_slot: "",
                  delivery_time: "",
                  payment_type: "",
                  delivery_instructions: "",
                }}
                onSubmit={async (values, { setSubmitting, setErrors }) => {
                  if (step === 4) {
                    const order = {
                      delivery_instructions: values.delivery_instructions,
                      delivery_date:
                        values.delivery_slot === "Today"
                          ? date.setDate(date.getUTCDate()).toLocaleString()
                          : date
                              .setDate(date.getUTCDate() + 1)
                              .toLocaleString(),
                      delivery_time: values.delivery_time,
                      payment_type: values.payment_type,
                      delivery_area: store[0].delivery_area,

                      delivery_charges: 45.0,
                      order_items: cart,
                      address: store[0].address,
                      city: store[0].city,
                      pincode: store[0].pincode,
                    };

                    setSubmitting(true);
                    try {
                      const { data } = await Axios.post(
                        "https://frozen-reef-15026.herokuapp.com/api/order",
                        order
                      );

                      setSubmitting(false);
                      history.replace({
                        pathname: "/order_confirm",
                        state: { order_id: data.id },
                      });
                    } catch (err) {
                      setSubmitting(false);
                      setErrors({ err: err.response.message });
                    }
                  }
                }}
              >
                {({
                  values,
                  handleSubmit,
                  handleChange,
                  setFieldValue,
                  errors,
                  validateForm,
                  isSubmitting,
                }) => (
                  <Form className="flex flex-col flex-1 overflow-y-auto">
                    {step === 3 ? (
                      <>
                        {backDrop()}

                        {addresses[0] && (
                          <AddressSelect
                            show={showAddress}
                            addresses={store}
                            address={store[0]}
                            noEdit={true}
                            setshowAddress={() => setshowAddress(false)}
                            setAddress={(e) => setAddress(e)}
                          ></AddressSelect>
                        )}

                        <div className="flex items-center justify-end px-4 py-1 bg-gray-140">
                          <span className="pr-4 font-extrabold">
                            I will pickup from store
                          </span>
                          <input
                            type="checkbox"
                            checked={step === 3}
                            onChange={() => {
                              if (step === 0) {
                                setStep(3);
                              } else {
                                setStep(0);
                              }
                            }}
                          />
                        </div>
                        <div className="h-4"></div>
                        <div
                          className="flex flex-col flex-1 overflow-y-auto "
                          style={{ height: 1000 }}
                        >
                          <div className="flex flex-col address">
                            <div className="px-4 py-1 font-bold bg-gray-140">
                              Select Delivery Address *
                            </div>
                            <div className="px-4 pt-3 bg-white">
                              <div className="flex ">
                                <div>
                                  <svg
                                    xmlns="http://www.w3.org/2000/svg"
                                    width="24"
                                    height="24"
                                    viewBox="0 0 24 24"
                                  >
                                    <path d="M12 0c-4.198 0-8 3.403-8 7.602 0 4.198 3.469 9.21 8 16.398 4.531-7.188 8-12.2 8-16.398 0-4.199-3.801-7.602-8-7.602zm0 11c-1.657 0-3-1.343-3-3s1.343-3 3-3 3 1.343 3 3-1.343 3-3 3z" />
                                  </svg>
                                </div>
                                <div className="w-full pl-3">
                                  <div className="font-bold house_no">
                                    {store[0].house_no}
                                  </div>
                                  <div>{store[0].address}</div>
                                  <div>
                                    {store[0].delivery_area},{store[0].city},
                                    {store[0].pincode}
                                  </div>
                                </div>
                              </div>
                              <div
                                className="flex justify-end font-bold text-green-700"
                                onClick={() => setshowAddress(true)}
                              >
                                Change
                              </div>
                            </div>
                          </div>
                          <div className="h-3"></div>
                          <div className="w-full h-4 bg-gray-140"></div>
                        </div>
                        <div className="mb-14 ">
                          <div className="flex items-center py-4 bg-gray-300 ">
                            <span className="w-16 h-0 border border-green-500"></span>
                            <span className="px-2 font-bold border border-green-500 rounded-full">
                              Delivery
                            </span>
                            <span className="w-16 h-0 border border-white"></span>
                            <span className="px-1 font-bold border border-white rounded-full ">
                              Billing
                            </span>
                          </div>
                          <div className="flex justify-end py-2">
                            <button
                              type="submit"
                              onClick={() => setStep(4)}
                              className="px-4 py-2 mr-2 text-white bg-green-600 rounded "
                            >
                              Place Order
                            </button>
                          </div>
                        </div>
                      </>
                    ) : (
                      <div className="flex flex-col flex-1 ">
                        <div className="flex-1 ">
                          {" "}
                          <div className="py-2">
                            <label
                              htmlFor="
                      Delivery Instructions"
                              className="block py-2 font-bold"
                            >
                              Delivery Instructions
                            </label>
                            <textarea
                              className="w-full h-32 py-2 border border-black"
                              value={values.delivery_instructions}
                              onChange={handleChange}
                              name="delivery_instructions"
                            ></textarea>
                          </div>
                          <div className="flex flex-col px-2 py-3 space-y-2 bg-gray-140">
                            <div className="flex justify-between text-gray-500">
                              <span>MRP Total</span>
                              <span className="">
                                <span>&#8377;</span>
                                <span className="pl-2 line-through">
                                  {total_price}
                                </span>
                              </span>
                            </div>
                            <div className="flex justify-between font-bold ">
                              <span>Sub Total</span>
                              <span className="">
                                <span>&#8377;</span>
                                <span className="ml-2">
                                  {total_selling_price}
                                </span>
                              </span>
                            </div>
                            <hr />
                            <div className="flex justify-between font-bold ">
                              <span>Delivery charge </span>
                              <span className="">
                                <span>&#8377;</span>
                                <span className="ml-2">45.00</span>
                              </span>
                            </div>
                            <div className="flex justify-between py-1 font-bold ">
                              <span>Total Bill</span>
                              <span className="">
                                <span>&#8377;</span>
                                <span className="ml-2">{total_bill}</span>
                              </span>
                            </div>
                            <div className="flex items-center justify-center text-white ">
                              <span className="px-1 font-bold bg-red-300 border">
                                {discount > 0
                                  ? `Order Now and Save ${discount}`
                                  : "Order Now"}
                              </span>
                            </div>
                          </div>
                        </div>
                        <div className="mb-14 ">
                          {" "}
                          <div className="flex items-center py-4 bg-gray-300 ">
                            <span className="w-16 h-0 border border-green-500"></span>
                            <span className="px-2 font-bold border border-green-500 rounded-full">
                              Delivery
                            </span>
                            <span className="w-16 h-0 border border-green-500"></span>
                            <span className="px-1 font-bold border border-green-500 rounded-full ">
                              Billing
                            </span>
                          </div>
                          {errors.err && errors ? (
                            <div className="py-2 pl-4 my-2 text-red-500 border border-red-600 ">
                              {errors.err}
                            </div>
                          ) : null}
                          <div className="flex justify-end py-2">
                            {!isSubmitting ? (
                              <button
                                type="submit"
                                className="px-4 py-2 mr-2 text-white bg-green-600 rounded "
                              >
                                Place Order
                              </button>
                            ) : (
                              <button className="inline-flex items-center px-3 py-2 mx-20 mt-4 text-base leading-6 text-white transition duration-150 ease-in-out bg-indigo-500 border border-transparent rounded-md shadow-sm focus:outline-none focus:ring-indigo-300 hover:bg-indigo-300 active:bg-indigo-700 ">
                                <svg
                                  class="animate-spin -ml-1 mr-3 h-5 w-5 text-white"
                                  xmlns="http://www.w3.org/2000/svg"
                                  fill="none"
                                  viewBox="0 0 24 24"
                                >
                                  <circle
                                    class="opacity-25"
                                    cx="12"
                                    cy="12"
                                    r="10"
                                    stroke="currentColor"
                                    stroke-width="4"
                                  ></circle>
                                  <path
                                    class="opacity-75"
                                    fill="currentColor"
                                    d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                                  ></path>
                                </svg>
                                Processing
                              </button>
                            )}
                          </div>
                        </div>
                      </div>
                    )}
                  </Form>
                )}
              </Formik>
            </div>{" "}
          </div>
        );
      }
    }
  };
  return <div>{render()}</div>;
}

export default PlaceOrder;
