import React, { useState, useEffect } from "react";
import ReactDOM from "react-dom";
import Select from "react-select";

import Axios, { CancelToken } from "axios";
import { useLocation, useHistory, useParams } from "react-router-dom";
import { useQuery } from "react-query";
const queryString = require("query-string");
function BrandFilter({
  toggleBrandFilter,

  setToggleBrandFilter,
}) {
  const filter = document.getElementById("filter");
  const root = document.getElementById("root");
  let location = useLocation();
  let pathname = location.pathname;

  const currentPage = new URLSearchParams(location.search).get("page");
  const [selectedBrands, setSelectedBrands] = useState([]);

  useEffect(() => {
    document.body.classList.add("overflow-hidden");
    root.classList.add("static");
    filter.classList.add(
      "h-screen",

      "flex",
      "items-center",
      "inset-0",
      "justify-center"
    );
    return () => {
      document.body.classList.remove("overflow-hidden");
      root.classList.remove("static");
      filter.classList.remove(
        "h-screen",

        "flex",
        "items-center",
        "inset-0",
        "justify-center"
      );
    };
  }, [filter.classList, root.classList]);
  useEffect(() => {
    const parsed = queryString.parse(location.search, {
      arrayFormat: "bracket",
    });
    if (parsed.brand) {
      setSelectedBrands(() => parsed.brand.map((i) => +i));
    }
  }, [location.search]);

  let { category, subCategory } = useParams();
  let category_id = category.split("-").pop();

  let product_category_id =
    subCategory === "all" ? "all" : subCategory.split("-").pop();
  let history = useHistory();
  // useEffect(() => {
  //   setSearch(history.location.search);
  //   console.log(search);
  // }, [history.location.search]);

  //   let dispatch = useDispatch();
  //   const ref = useRef();
  //   let location = useLocation();
  //   const products = useSelector((state) => state.productBuilder.products);
  const { data, status } = useQuery(
    `${history.location.pathname}`,
    () => {
      const source = CancelToken.source();
      let url;
      if (product_category_id === "all") {
        url = `https://frozen-reef-15026.herokuapp.com/api/brands?category_id=${category_id}&page=${+currentPage}`;
      } else {
        url = `https://frozen-reef-15026.herokuapp.com/api/brands?category_id=${category_id}&product_category_id=${product_category_id}&page=${+currentPage}`;
      }

      const promise = Axios.get(url, { params: { cancelToken: source.token } });

      // Cancel the request if React Query calls the `promise.cancel` method
      promise.cancel = () => {
        source.cancel("Query was cancelled by React Query");
      };

      return promise;
    },
    {
      refetchOnMount: false,
      refetchOnWindowFocus: false,
      cacheTime: 600000,
      staleTime: 60000,
    }
  );
  const applyFilter = () => {
    const selectedBrandsStringify = queryString.stringify(
      { brand: selectedBrands, page: 0, sort: "popularity" },
      {
        arrayFormat: "bracket",
      }
    );

    history.push({ pathname: pathname, search: selectedBrandsStringify });
    setToggleBrandFilter();
  };

  const handleChange = (e) => {
    setSelectedBrands(Array.isArray(e) ? e.map((i) => i.value) : []);
  };
  let options = [];
  options =
    data &&
    data.data.map((i) => {
      return { value: i.id, label: i.name };
    });
  // const brand = useSelector((state) => state.productBuilder.brand);
  // const brandFilter = useSelector(
  //   (state) => state.productBuilder.brand_filters
  // );
  // const dispatch = useDispatch();
  // const dispatchFilter = () => {
  //   dispatch(actions.fetchBrandFilter(category, subCategory));
  //   settoggleBrandFilter();
  // };

  // console.log("render");
  const render = () => {
    if (status === "loading")
      return (
        <>
          <div
            className="absolute inset-0 z-10 bg-black opacity-25 dark:opacity-80"
            onClick={setToggleBrandFilter}
          ></div>{" "}
          <div
            className={
              toggleBrandFilter
                ? "fixed bottom-0 z-20 flex flex-col w-full py-4 opacity-100 transition-opacity duration-500 bg-white dark:text-gray-400 dark:bg-mine-shaft-500"
                : "fixed bottom-0 z-20 flex flex-col w-full py-4 opacity-0 pointer-events-none transition-opacity duration-500 bg-white dark:bg-mine-shaft-500 dark:text-gray-400 "
            }
          >
            <p>loading...</p>
          </div>
        </>
      );

    if (status === "error")
      return (
        <>
          <div
            className="absolute inset-0 z-10 bg-black opacity-25 dark:opacity-80"
            onClick={setToggleBrandFilter}
          ></div>{" "}
          <div
            className={
              toggleBrandFilter
                ? "fixed bottom-0 z-20 flex flex-col w-full py-4 opacity-100 transition-opacity duration-500 bg-white dark:text-gray-400 dark:bg-mine-shaft-500 "
                : "fixed bottom-0 z-20 flex flex-col w-full py-4 opacity-0 pointer-events-none transition-opacity duration-500 bg-white dark:text-gray-400 dark:bg-mine-shaft-500 "
            }
          >
            <p>loading...</p>
          </div>
        </>
      );

    return (
      <>
        <div
          className="absolute inset-0 z-10 bg-black opacity-25 dark:opacity-80"
          onClick={setToggleBrandFilter}
        ></div>
        <div
          className={
            toggleBrandFilter
              ? "fixed bottom-0 z-20 flex flex-col w-full py-4 opacity-100 transition-opacity duration-500 bg-white dark:bg-mine-shaft-500 dark:text-gray-400 "
              : "fixed bottom-0 z-20 flex flex-col w-full py-4 opacity-0 pointer-events-none transition-opacity duration-500 bg-white dark:bg-mine-shaft-500 dark:text-gray-400"
          }
        >
          <div className="flex items-center justify-between px-4 py-2 font-bold text-gray-600 border-b border-gray-500 dark:text-white ">
            <span> Brand Filter</span>
            <button
              className="px-6 py-2 text-white bg-red-600 rounded shadow dark:text-gray-200 dark:bg-blue-400 focus:outline-none "
              onClick={applyFilter}
            >
              Apply
            </button>
          </div>
          <div className="" style={{ height: 400 }}>
            <Select
              className="dark:text-gray-900 dark:bg-mine-shaft-700"
              options={options}
              theme={"neutral180"}
              onChange={handleChange}
              isMulti={true}
              value={options.filter((i) => selectedBrands.includes(i.value))}
              isClearable={true}
              hideSelectedOptions={true}
              autoFocus={true}
              closeMenuOnSelect={false}
              placeholder={"Choose Brand Filter"}
            ></Select>
          </div>
        </div>
      </>
    );
  };
  return ReactDOM.createPortal(render(), document.getElementById("filter"));
}

export default BrandFilter;
