import React from "react";
import { Formik, Form } from "formik";

import * as Yup from "yup";
import { useSelector } from "react-redux";
import Axios from "axios";
import string_to_slug from "../utils/slug";
import { useHistory } from "react-router-dom";
import { CustomInput } from "../utils/CustomInput";
Axios.defaults.withCredentials = true;

function AddProductCategory() {
  let history = useHistory();

  const parseAndHandleChange = (value, setFieldValue, id) => {
    const parsed = parseInt(value, 10);
    setFieldValue(id, parsed);
  };

  const category = useSelector((state) => state.categoryBuilder.category);
  let categoryOptions = [];
  category.map((i) => {
    categoryOptions.push({ name: i.name, id: i.id });
    return i;
  });
  async function _handleSubmit(values, actions) {
    let category = values.category;
    console.log(category);

    let slug = string_to_slug(values.category.name);
    values.category.slug = slug;
    console.log(values);
    const { data } = await Axios.post(
      "https://frozen-reef-15026.herokuapp.com/api/category",
      values
    );
    history.push("/");
  }
  return (
    <div>
      <div className="flex flex-col h-screen ">
        <div className="flex py-2 text-white bg-red-500 ">
          <div className="flex items-center justify-center">
            <div className="px-2 " onClick={() => history.goBack()}>
              <svg
                fill="currentColor"
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                viewBox="0 0 24 24"
              >
                <path d="M16.67 0l2.83 2.829-9.339 9.175 9.339 9.167-2.83 2.829-12.17-11.996z" />
              </svg>
            </div>
            <div className="font-bold">Add Product Category</div>
          </div>
        </div>
        <div className="flex flex-col flex-1 pt-4 mx-8 ">
          <Formik
            initialValues={{
              category: {
                name: "",

                category_id: "",
              },
            }}
            validationSchema={Yup.object().shape({
              category: Yup.object().shape({
                name: Yup.string().required("Please enter Category Name"),

                category_id: Yup.string().required("Select a Category"),
              }),
            })}
            onSubmit={(values, actions) => _handleSubmit(values, actions)}
          >
            {({
              values,

              handleChange,
              setFieldValue,
              isSubmitting,
              validateForm,
              errors,
            }) => {
              return (
                <Form className="flex flex-col flex-1 overflow-y-auto ">
                  <div className="flex flex-col justify-between flex-1 mx-4 space-y-2 overflow-y-auto ">
                    <div>
                      <CustomInput
                        label="Product Category Name"
                        name="category.name"
                        type="text"
                        onChange={handleChange}
                        value={values.category.name}
                        placeholder=" Name"
                      ></CustomInput>
                      <div className="flex flex-col pt-2 space-y-2">
                        <div className="font-bold"> Category</div>
                        <select
                          onChange={(event) =>
                            parseAndHandleChange(
                              event.target.value,
                              setFieldValue,
                              "category.category_id"
                            )
                          }
                          placeholder=""
                          name="category.category_id"
                          value={values.category.category_id}
                        >
                          <option value="" key="">
                            Select Category
                          </option>
                          {categoryOptions.map((i) => (
                            <option key={i.id} value={i.id}>
                              {i.name}
                            </option>
                          ))}
                        </select>
                      </div>
                    </div>
                  </div>
                  <div className="flex justify-end py-2 mb-20">
                    <button
                      type="submit"
                      onClick={() =>
                        validateForm().then((e) => {
                          if (!e) {
                            return _handleSubmit(values);
                          }
                          return;
                        })
                      }
                      className="px-4 py-2 mr-2 text-white bg-green-600 rounded "
                    >
                      Next
                    </button>
                  </div>
                </Form>
              );
            }}
          </Formik>
        </div>
      </div>
    </div>
  );
}

export default AddProductCategory;
