import * as actionTypes from "./actionTypes";
import Axios from "axios";

export const fetchCategorySuccess = (category) => {
  return {
    type: actionTypes.FETCH_PRODUCT_CATEGORY,
    category: category,
  };
};
// export const fetchCategoryFailed = () => {
//   return {
//     type: actionTypes.fetchCategoryFailed,
//   };
// };

export const initCategory = () => {
  //   if (data.length < 0) {
  //     return (dispatch) => dispatch(fetchCategoryFailed());
  //   //   }
  return async (dispatch) => {
    const { data } = await Axios.get(
      "https://frozen-reef-15026.herokuapp.com/api/category"
    );

    dispatch(fetchCategorySuccess(data));
  };
};
