import React from "react";
import { useSelector } from "react-redux";

import { useHistory } from "react-router-dom";
function Auth() {
  const auth = useSelector((state) => state.authBuilder.isSignedIn);
  const history = useHistory();
  const onSignInClick = () => {
    history.push("/login");
  };

  const authButton = () => {
    if (auth === null) {
      return null;
    } else if (!auth) {
      return (
        <div
          onClick={onSignInClick}
          className="flex items-center justify-between px-2 py-2 mt-4 mb-2 font-bold tracking-tighter text-white bg-green-600 outline-none"
        >
          <div className="flex items-center space-x-3">
            <svg
              fill="currentColor"
              xmlns="http://www.w3.org/2000/svg"
              width="24"
              height="24"
              viewBox="0 0 24 24"
            >
              <path
                d="M7 11v2.4h3.97c-.16 1.029-1.2 3.02-3.97 3.02-2.39 0-4.34-1.979-4.34-4.42 0-2.44 1.95-4.42 4.34-4.42 1.36 0 2.27.58 2.79 1.08l1.9-1.83c-1.22-1.14-2.8-1.83-4.69-1.83-3.87 0-7 3.13-7 7s3.13 7 7 7c4.04 0 6.721-2.84 6.721-6.84 0-.46-.051-.81-.111-1.16h-6.61zm0 0 17 2h-3v3h-2v-3h-3v-2h3v-3h2v3h3v2z"
                fillRule="evenodd"
                clipRule="evenodd"
              />
            </svg>
            <span className="leading-3">Login / Register</span>
          </div>
          <svg
            fill="white"
            xmlns="http://www.w3.org/2000/svg"
            width="16"
            height="16"
            viewBox="0 0 24 24"
          >
            <path d="M13.025 1l-2.847 2.828 6.176 6.176h-16.354v3.992h16.354l-6.176 6.176 2.847 2.828 10.975-11z" />
          </svg>
        </div>
      );
    }
  };

  return <div className="">{authButton()}</div>;
}

export default Auth;
