import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import * as actions from "../store/actions";
import { useHistory } from "react-router-dom";
import Spinner from "../utils/Spinner/spinner";
function Order() {
  let dispatch = useDispatch();
  const orders = useSelector((state) => state.orderBuilder.orders);
  const loading = useSelector((state) => state.orderBuilder.loading);
  const [filter, setFilter] = useState([]);
  useEffect(() => {
    dispatch(actions.fetchOrders());
  }, [dispatch]);
  useEffect(() => {
    setFilter(orders);
  }, [orders]);
  const [value, setValue] = useState("");

  const onChange = (e) => {
    let value = e.target.value;
    setValue(e.target.value);
    if (e.target.value === "") {
      setFilter(orders);
    } else {
      setFilter(() => {
        let f = orders.filter((i) => {
          if (i.id.toString().includes(value)) {
            return false;
          } else return true;
        });
        return f;
      });
    }
  };
  let history = useHistory();
  const render = () => {
    if (loading) {
      return (
        <div className="flex flex-col items-center w-full h-full">
          <div className="flex items-center justify-center flex-1">
            <Spinner></Spinner>
          </div>
        </div>
      );
    } else if (orders) {
      return (
        <div>
          <div className="flex flex-col h-screen ">
            <div className="flex items-center text-white bg-red-500 ">
              <div className="flex items-center justify-center py-2">
                <div className="px-2 " onClick={() => history.goBack()}>
                  <svg
                    fill="currentColor"
                    xmlns="http://www.w3.org/2000/svg"
                    width="16"
                    height="16"
                    viewBox="0 0 24 24"
                  >
                    <path d="M16.67 0l2.83 2.829-9.339 9.175 9.339 9.167-2.83 2.829-12.17-11.996z" />
                  </svg>
                </div>

                <div className="pl-4 ">Order History</div>
              </div>
            </div>
            <div className="flex flex-col flex-1 overflow-y-auto ">
              <div className="relative flex px-1 py-2 search">
                <span className="absolute inset-y-0 flex items-center pl-2">
                  <svg
                    className="absolute "
                    xmlns="http://www.w3.org/2000/svg"
                    width="16"
                    height="16"
                    viewBox="0 0 24 24"
                  >
                    <path d="M23.822 20.88l-6.353-6.354c.93-1.465 1.467-3.2 1.467-5.059.001-5.219-4.247-9.467-9.468-9.467s-9.468 4.248-9.468 9.468c0 5.221 4.247 9.469 9.468 9.469 1.768 0 3.421-.487 4.839-1.333l6.396 6.396 3.119-3.12zm-20.294-11.412c0-3.273 2.665-5.938 5.939-5.938 3.275 0 5.94 2.664 5.94 5.938 0 3.275-2.665 5.939-5.94 5.939-3.274 0-5.939-2.664-5.939-5.939z" />
                  </svg>
                </span>
                <input
                  placeholder="Search Order"
                  value={value}
                  onChange={onChange}
                  className="w-full pl-8 pr-8 border-b rounded shadow-sm outline-none focus:shadow-outline"
                />
              </div>
              {filter.length
                ? filter.map((i) => {
                    return (
                      <div key={i.id}>
                        <div
                          className="flex justify-between px-2 py-3 border-b border-gray-300"
                          onClick={() =>
                            history.push({
                              pathname: "/order",
                              search: `id=${i.id}`,
                              state: { order: i },
                            })
                          }
                        >
                          <div>
                            <div className="pb-3 font-bold">{i.id}</div>
                            <div className="pb-1 text-gray-600">
                              Delivery time
                            </div>
                            <div>{i.delivery_date}</div>
                            <div>{i.delivery_time}</div>
                          </div>

                          <div>
                            <div className="pb-3 font-bold text-green-500">
                              {i.order_status}
                            </div>
                            <div className="pb-2 text-gray-600">
                              Payment Type
                            </div>
                            <div className="font-bold">Cash on Delivery</div>
                          </div>
                        </div>
                      </div>
                    );
                  })
                : null}
            </div>
          </div>
        </div>
      );
    } else {
      return null;
    }
  };
  return <>{render()}</>;
}

export default Order;
