import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import * as actions from "../store/actions";
import { useHistory, Redirect, useLocation } from "react-router-dom";

function OrderConfirm() {
  let location = useLocation();
  const orderId = location.state.order_id;
  const purchased = useSelector((state) => state.orderBuilder.purchased);

  const history = useHistory();
  let dispatch = useDispatch();
  useEffect(() => {
    return () => dispatch(actions.purchaseReset());
  });
  console.log(purchased);
  const render = () => {
    if (!orderId) {
      return <Redirect to="/"></Redirect>;
    } else {
      return (
        <div className="relative flex flex-col h-screen ">
          <div className="flex items-center text-white bg-red-500 ">
            <div className="flex items-center justify-center py-2">
              <div className="px-2 " onClick={() => history.replace("/")}>
                <svg
                  fill="currentColor"
                  xmlns="http://www.w3.org/2000/svg"
                  width="16"
                  height="16"
                  viewBox="0 0 24 24"
                >
                  <path d="M16.67 0l2.83 2.829-9.339 9.175 9.339 9.167-2.83 2.829-12.17-11.996z" />
                </svg>
              </div>

              <div className="pl-4 ">Order Confirmation</div>
            </div>
          </div>

          <div className="flex flex-col items-center flex-1 px-4 py-24 ">
            <div className="py-4 text-xl font-bold">THANK YOU!</div>
            <div className="py-2 font-semibold">
              {" "}
              Your order id is {orderId}
              We are getting your order ready right away,and you will receive an
              order confirmation when order is ready.In the Mean time you can
              explore more products.You can check the Order Status through this
              link.
            </div>
            <button
              className="px-2 py-2 text-white bg-black rounded-full "
              onClick={() => history.replace("/orders")}
            >
              Move to Order History
            </button>
          </div>
        </div>
      );
    }
  };

  return <>{render()}</>;
}

export default OrderConfirm;
