import React, { useState, useEffect } from "react";
import { useHistory, useLocation } from "react-router-dom";
import Axios from "axios";
import Spinner from "../utils/Spinner/spinner";
Axios.defaults.withCredentials = true;

function Order() {
  let history = useHistory();
  let location = useLocation();
  console.log(history);
  const id = location.search.split("?id=")[1];
  const { order } = location.state;

  const [orderDetail, setOrderDetails] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      const { data } = await Axios.get(
        `https://frozen-reef-15026.herokuapp.com/api/order?q=${+id}`
      );
      setOrderDetails(data);
    };
    fetchData();
  }, [id]);
  console.log(orderDetail);

  const render = () => {
    if (orderDetail.length) {
      console.log(orderDetail);
      return (
        <div className="flex flex-col h-screen ">
          <div className="flex items-center text-white bg-red-500 ">
            <div className="flex items-center justify-center py-2">
              <div className="px-2 " onClick={() => history.goBack()}>
                <svg
                  fill="currentColor"
                  xmlns="http://www.w3.org/2000/svg"
                  width="16"
                  height="16"
                  viewBox="0 0 24 24"
                >
                  <path d="M16.67 0l2.83 2.829-9.339 9.175 9.339 9.167-2.83 2.829-12.17-11.996z" />
                </svg>
              </div>

              <div className="pl-4 ">Order No. {`  ${id}`}</div>
            </div>
          </div>
          <div className="flex flex-col flex-1 overflow-y-auto ">
            <div className="flex justify-between px-2 py-3 border-b border-gray-200 divide-x-2 divide-gray-300">
              <div className="w-1/2">
                <div className="pb-3 font-bold">{order.id}</div>
                <div className="pb-1 text-gray-600">Delivery time</div>
                <div>{order.delivery_date}</div>
                <div>{order.delivery_time}</div>
                <div>
                  <div className="py-1 text-gray-600">Payment Type</div>
                  <div className="py-1 font-bold ">Cash on Delivery</div>
                </div>
              </div>
              <div className="flex flex-col w-1/2 text-right">
                <div>
                  <div className="text-gray-600">Address</div>
                  <div className="font-bold">
                    {order.address},{order.city},{"Andhra Pradesh"}-
                    {order.pincode}
                  </div>
                </div>
                <div>
                  <div className="text-gray-600">Delivery Area</div>
                  <div className="font-bold">{order.delivery_area}</div>
                </div>
                <div className="font-bold text-red-600">
                  {orderDetail.length} Item(s)
                </div>
                <div className="font-bold">  {order.total.toFixed(2)}</div>
              </div>
            </div>

            <div className="py-2" />
            {order.delivery_instructions && (
              <div className="py-3 bg-gray-200">
                <div className="px-4 pb-2 font-medium text-black">Notes</div>
                <div className="px-4">{order.delivery_instructions}</div>
              </div>
            )}
            <div className="py-3 bg-white"></div>
            <div className="bg-gray-200">
              <div className="px-2 py-2 font-bold">Billed Items</div>

              {orderDetail.map((i) => (
                <div key={i.id}>
                  <div className="flex px-1 bg-white">
                    <div className="w-24 h-24 px-2 ">
                      <img
                        src={i.skus.images[0].image}
                        alt={i.productId.name}
                      />
                    </div>

                    <div className="flex flex-col justify-center flex-1">
                      <div className="font-semibold">{i.productId.name}</div>
                      <div className="flex items-center justify-between">
                        <div>
                          500 gm *{" "}
                          <span className="px-2 py-1 bg-gray-300 rounded">
                            {i.quantity}
                          </span>
                        </div>
                        <div className="pr-1 font-bold">
                           {i.price.toFixed(2)}
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              ))}

              <div className="pt-4">
                <div className="flex justify-between px-2 pb-1 font-bold border-b border-gray-200">
                  <div className=""> Sub Total</div>

                  <div>  {order.total.toFixed(2)}</div>
                </div>
                <div className="flex flex-col space-y-2">
                  <div className="flex justify-between px-2 font-bold">
                    <div className=""> SGST</div>

                    <div>  {order.total.toFixed(2)}</div>
                  </div>
                  <div className="flex justify-between px-2 font-bold">
                    <div className=""> SGST</div>

                    <div>  {order.total.toFixed(2)}</div>
                  </div>
                  <div className="flex justify-between px-2 font-bold">
                    <div className=""> Delivery Charge</div>

                    <div>  {order.delivery_charges.toFixed(2)}</div>
                  </div>
                </div>
                <div className="flex justify-between px-2 py-3 font-bold">
                  <div className=""> SGST</div>

                  <div>  {order.total.toFixed(2)}</div>
                </div>
                <div className="flex justify-between px-2 font-bold">
                  <div className=""> Total Bill</div>

                  <div>  {order.total.toFixed(2)}</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      );
    } else {
      return (
        <div className="flex flex-col items-center w-full h-full">
          <div className="flex items-center justify-center flex-1">
            <Spinner></Spinner>
          </div>
        </div>
      );
    }
  };
  return <>{render()}</>;
}

export default Order;
