import * as actionTypes from "../actions/actionTypes";

export const updateObject = (oldObject, updatedProperties) => {
  return {
    ...oldObject,
    ...updatedProperties,
  };
};

const initialState = {
  search: [],
  loading: false,
};

const fetchSearchStart = (state, action) => {
  return updateObject(state, { loading: true });
};
const setSearch = (state, action) => {
  return updateObject(state, {
    search: [...state.search, ...action.data],
    loading: false,
  });
};
const resetSearch = (state, action) => {
  return updateObject(state, { search: [] });
};
const fetchSearchFailed = (state, action) => {
  return updateObject(state, { loading: false });
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    // case actionTypes.PURCHASE_INIT:
    //   return purchaseInit(state, action);
    case actionTypes.FETCH_SEARCH_LOADING:
      return fetchSearchStart(state, action);
    case actionTypes.SET_SEARCH:
      return setSearch(state, action);
    case actionTypes.FETCH_SEARCH_FAILED:
      return fetchSearchFailed(state, action);
    case actionTypes.RESET_SEARCH:
      return resetSearch(state, action);

    default:
      return state;
  }
};

export default reducer;
