import React, { useEffect } from "react";
import Header from "../components/Header";
import * as actions from "../store/actions/index";
import { useDispatch, useSelector } from "react-redux";
import Product from "../components/product";
import { SkeletonComponent } from "../utils/SkeletonComponent";

function SearchProduct(props) {
  const { name } = props.match.params;

  const products = useSelector((state) => state.productBuilder.products);
  const loading = useSelector((state) => state.productBuilder.loading);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(actions.setQuery(name));

    dispatch(actions.initSearchProduct());
  }, [dispatch, name]);
  console.log(products);
  const product = () => {
    if (loading) {
      return (
        <div className="min-h-screen divide-y divide-gray-200 ">
          <div className="px-4 py-2">
            <div className="animate-pulse">
              <SkeletonComponent type="long"></SkeletonComponent>
            </div>
          </div>
          <div className="px-4 py-2">
            <div
              className="animate-pulse"
              style={{
                animationFillMode: "backwards",
                animationDelay: "150ms",
              }}
            >
              <SkeletonComponent type="short"></SkeletonComponent>
            </div>
          </div>
          <div className="px-4 py-2">
            <div
              className="animate-pulse"
              style={{
                animationFillMode: "backwards",
                animationDelay: "300ms",
              }}
            >
              <SkeletonComponent type="long"></SkeletonComponent>
            </div>
          </div>
          <div className="px-4 py-2">
            <div
              className="animate-pulse"
              style={{
                animationFillMode: "backwards",
                animationDelay: "450ms",
              }}
            >
              <SkeletonComponent type="short"></SkeletonComponent>
            </div>
          </div>
        </div>
      );
    } else if (products) {
      return (
        <div className=" component">
          {products.map((i, index) => {
            return (
              <Product key={index} product={i}>
                {" "}
                {i.name}
              </Product>
            );
          })}
        </div>
      );
    }
  };

  return (
    <div className="flex flex-col h-screen">
      <Header></Header>
      <div className="flex-1 px-2 overflow-y-scroll home">{product()}</div>
    </div>
  );
}

export default SearchProduct;
