import React, { useState } from "react";
import Header from "../components/Header";
import SubCategoryHeader from "../components/subCategoryHeader";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Drawer from "../components/Drawer";

import BackDrop from "../components/BackDrop";
import ProductContainer from "../components/ProductContainer";
import Filters from "../components/Filters";
const SubCategory = React.memo(() => {
  console.log("render");

  const [showDrawer, setshowDrawer] = useState(false);
  const [showFilters, setshowFilters] = useState(false);
  const backDropClick = () => {
    setshowDrawer(false);
  };
  let backdrop;
  if (showDrawer) {
    backdrop = <BackDrop onClick={backDropClick}></BackDrop>;
  }
  let cssClass =
    showDrawer || showFilters
      ? "relative flex flex-col h-screen overflow-hidden"
      : "relative flex flex-col dark:bg-mine-shaft-500 ";

  return (
    <>
      <div className={cssClass + " dark:text-gray-200"}>
        <Drawer show={showDrawer} onClick={() => setshowDrawer(false)}></Drawer>
        {backdrop}
        <Header onClick={() => setshowDrawer(true)} />
        {/* <SubCategoryDrawer></SubCategoryDrawer> */}
        <ToastContainer
          position="top-left"
          autoClose={1500}
          hideProgressBar={false}
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnFocusLoss
          draggable
          pauseOnHover
        />
        <SubCategoryHeader></SubCategoryHeader>
        <Filters
          showFilters={showFilters}
          openBackDrop={() => setshowFilters((e) => !e)}
        ></Filters>
        <ProductContainer></ProductContainer>{" "}
      </div>
    </>
  );
});

export default SubCategory;
