import React, { useState, useEffect } from "react";
import Cart from "./cartButton";
import { useHistory } from "react-router-dom";

const Product = ({ product, id }) => {
  const [item, setItem] = useState();

  const [sku, setSku] = useState();
  useEffect(() => {
    // if (product) {
    //   setItem(product);
    //   setSku(() => {
    //     let isDefault;
    //     product.skus.find((i) => {
    //       if (i.isDefault) {
    //         isDefault = i;
    //       }
    //     });
    //     isDefault = isDefault ? isDefault : product.skus[0];
    //     return isDefault;
    //   });
    // }
    if (product.skus.length) {
      setItem(product);

      setSku(() => product.skus[0]);
    } else {
      setItem(product);
    }
  }, [product]);
  let history = useHistory();
  const onChange = (e) => {
    const selectedType = e.target.value;
    console.log(selectedType);
    const selectedChoice = item.skus.find((i) => i.id === +selectedType);

    setSku(selectedChoice);
  };
  console.log(item);
  if (item && sku && sku.stock > 0) {
    return (
      <>
        <div
          id={id}
          className="flex flex-col pb-4 space-y-2 border-b border-gray-400 product"
        >
          <div className="flex px-2 space-x-2 product-detail ">
            <div className="relative flex flex-col w-1/3 overflow-hidden cursor-pointer ">
              <img
                onClick={() => history.push(`/product/${product.id}`)}
                src={item.skus[0].images[0].image}
                className="absolute object-contain w-full h-full"
                alt={item.name}
              />{" "}
              {sku.discount > 0 && (
                <div className="flex items-center justify-center text-center">
                  <span className="absolute z-10 px-2 py-1 leading-none text-white bg-red-500 rounded top-1 h6 ">
                    {parseFloat(sku.discount).toFixed(2)} OFF
                  </span>
                </div>
              )}
            </div>

            <div className="flex flex-col w-2/3 space-y-1">
              <div className="font-semibold ">{item.name}</div>

              <select
                onChange={onChange}
                value={sku.name}
                className="w-40 text-gray-700 bg-gray-200 dark:bg-gray-600 dark:text-gray-200"
              >
                {item.skus.map((i) => {
                  return (
                    <option value={i.id} key={i.id}>
                      {i.sku}
                    </option>
                  );
                })}
              </select>
              <div className="flex justify-between">
                <div className="flex flex-col">
                  {sku.discount > 0 ? (
                    <div className="flex items-baseline space-x-1 initial price">
                      <span> &#8377; </span>
                      <div className="line-through">
                        <span className="">{sku.mrp}</span>
                      </div>
                    </div>
                  ) : null}

                  <div className="flex items-center justify-between ">
                    <div className="font-bold price">
                      <span> &#8377; </span>
                      <span>{sku.sp}</span>
                    </div>
                  </div>
                </div>
                <Cart product={item} sku={sku}></Cart>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  } else if (product && sku && sku.stock <= 0) {
    return (
      <>
        <div className="flex flex-col pb-4 space-y-2 border-b border-gray-400 product">
          <div className="flex px-2 space-x-2 product-detail">
            <div className="relative flex flex-col w-1/3 overflow-hidden cursor-pointer pb-2/3">
              <img
                onClick={() => history.push(`/product/${product.id}`)}
                src={sku.images[0].image}
                className="absolute object-contain w-full h-full"
                alt={item.name}
              />
              {sku.discount > 0 && (
                <div className="flex items-center justify-center text-center">
                  <span className="absolute z-10 px-2 py-1 leading-none text-white bg-red-500 rounded top-1 h6 ">
                    {parseFloat(sku.discount).toFixed(2)} OFF
                  </span>
                </div>
              )}
              <div className="absolute inset-0 flex items-center justify-center text-center">
                <div className="z-10 flex justify-center w-16 h-16 px-2 py-2 text-gray-600 bg-gray-400 ">
                  <span className="">Out Of Stock</span>
                </div>
              </div>
            </div>
            <div className="flex flex-col w-2/3 space-y-1">
              <div className="font-semibold ">{item.name}</div>

              <select
                onChange={onChange}
                value={sku.name}
                className="w-40 text-gray-700 bg-gray-200 dark:bg-gray-600 dark:text-gray-200"
              >
                {item.skus.map((i) => {
                  return (
                    <option value={i.sp} key={i.sp}>
                      {i.sp}
                    </option>
                  );
                })}
              </select>
              <div className="flex justify-between">
                <div className="flex flex-col">
                  {sku.discount > 0 ? (
                    <div className="flex items-baseline space-x-1 initial price">
                      <span> &#8377; </span>
                      <div className="line-through">
                        <span className="">{sku.mrp}</span>
                      </div>
                    </div>
                  ) : null}

                  <div className="flex items-center justify-between ">
                    <div className="font-bold price">
                      <span> &#8377; </span>
                      <span>{sku.sp}</span>
                    </div>
                  </div>
                </div>
                <div className="flex items-center justify-between rounded-md focus:outline-none">
                  <button
                    className="py-2 pl-2 pr-8 leading-none text-white bg-gray-400 rounded-l-md "
                    disabled={true}
                    style={{ WebkitTapHighlightColor: "transparent" }}
                  >
                    Add
                  </button>
                  <div className="py-1 leading-none text-white bg-gray-400 outline-none rounded-r-md focus:outline-none">
                    <svg
                      className="w-8 h-6"
                      fill="currentColor"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth="2"
                      viewBox="0 0 24 24"
                      stroke="currentColor"
                    >
                      <path d="M12 4v16m8-8H4"></path>
                    </svg>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  } else {
    return null;
    //   <div className="flex items-center justify-center pt-3">
    //     <Spinner></Spinner>
    //   </div>
    // );
  }
};

export default Product;
