import React from "react";
import { Link } from "react-router-dom";

function ProductCategory({ category }) {
  console.log(window.outerWidth);
  const subCat = category.map((i) => i.subCat.map((i) => i.name));
  const truncate = (array_word) => {
    const word = array_word.join(", ");
    if (word.length > (window.outerHeight * 0.5) / 8) {
      return word.substring(0, (window.outerHeight * 0.5) / 8).concat("...");
    } else return word;
  };

  return (
    <div>
      <div className="py-4 font-bold">Shop by Category</div>
      <div className="flex flex-col space-y-2">
        {" "}
        {category.map((i, index) => (
          <Link
            to={{
              pathname: `/products/${i.slug}-${i.id}/all`,
              search: `?page=0`,
            }}
            key={index}
            className="flex px-1 py-2 space-x-1 border border-gray-400 rounded shadow-md"
          >
            <div className="w-3/12 h-24 ">
              <img src={i.image_url} alt={i.name} className="h-24 " />
            </div>
            <div className="flex flex-col w-3/5 right ">
              <div className="pb-2 font-bold">{i.name}</div>
              <div className="flex-grow-0 h-8">{truncate(subCat[index])}</div>
            </div>
            <div className="flex items-center justify-center text-gray-700 border-solid rounded-lg cursor-pointer w-3/20 ">
              <span className="px-2 py-2 bg-white rounded-lg shadow-lg dark:bg-gray-200">
                <svg
                  className="block w-3 h-3 overflow-hidden shadow-lg stroke-current "
                  style={{ strokeWidth: 4 }}
                  viewBox="0 0 32 32"
                  xmlns="http://www.w3.org/2000/svg"
                  aria-hidden="true"
                  role="presentation"
                  focusable="false"
                >
                  <g fill="none">
                    <path d="m12 4 11.2928932 11.2928932c.3905243.3905243.3905243 1.0236893 0 1.4142136l-11.2928932 11.2928932"></path>
                  </g>
                </svg>
              </span>
            </div>
          </Link>
        ))}
      </div>
    </div>
  );
}

export default ProductCategory;
