import * as actionTypes from "./actionTypes";
import Axios from "axios";
Axios.defaults.withCredentials = true;

export const signIn = (userData) => {
  console.log(userData);
  return { type: actionTypes.SIGN_IN, payload: userData };
};
export const currentUser = () => {
  return async (dispatch) => {
    // const token = JSON.parse(localStorage.getItem("jwt"));
    try {
      const { data } = await Axios.get(
        "https://frozen-reef-15026.herokuapp.com/api/current"
      );
      if (data.response.id)
        dispatch({ type: actionTypes.CURRENT_USER, payload: data });
    } catch (err) {
      dispatch({ type: actionTypes.SIGN_OUT });
    }
  };
};

const setAddress = (data) => {
  return { type: actionTypes.SET_ADDRESS, payload: data };
};
export const addAddress = (data) => {
  return { type: actionTypes.ADD_ADDRESS, payload: data };
};

export const deleteAddress = (data) => {
  console.log(data);
  return async (dispatch) => {
    dispatch(addressLoading());
    await Axios.delete("https://frozen-reef-15026.herokuapp.com/api/address", {
      data: { id: data },
    });
    return dispatch({ type: actionTypes.DELETE_ADDRESS, payload: data });
  };
};
const addressLoading = () => {
  return { type: actionTypes.FETCH_ADDRESS_LOADING };
};

export const fetchAddress = () => {
  return async (dispatch) => {
    dispatch(addressLoading());
    const { data } = await Axios.get(
      "https://frozen-reef-15026.herokuapp.com/api/address"
    );
    if (data.length) dispatch(setAddress(data));
    else dispatch(setAddress([]));
  };
};
export const signOut = () => {
  localStorage.removeItem("jwt");
  return async (dispatch) => {
    await Axios.get("https://frozen-reef-15026.herokuapp.com/api/logout", {
      withCredentials: true,
    });
    dispatch({ type: actionTypes.SIGN_OUT });
  };
};
