import React, { useRef } from "react";
import { NavLink, useParams } from "react-router-dom";
import { useSelector } from "react-redux";

const SubCategoryHeader = () => {
  const ref = useRef([]);

  let { category, subCategory } = useParams();
  console.log(useParams());

  category = category.split("-").pop();

  subCategory = subCategory !== "all" ? subCategory.split("-").pop() : "all";

  let categoryFind = useSelector((state) =>
    state.categoryBuilder.category.find((i) => i.id === +category)
  );
  let categoryClass;
  subCategory === "all"
    ? (categoryClass =
        "bg-blue-800    font-bold flex-shrink-0 px-2 py-1  dark:bg-blue-400  rounded-md tracking-wider")
    : (categoryClass =
        " flex-shrink-0 px-2 py-1 tracking-wider bg-white rounded-md dark:bg-black");

  // const selectedIndex =
  //   categoryFind &&
  //   categoryFind.product_category.findIndex((i) => {
  //     return i.slug === subCategoryFind.slug;
  //   });
  // const select =
  //   categoryFind && categoryFind.product_category.splice(selectedIndex, 1)[0];

  // categoryFind && categoryFind.product_category.unshift(select);

  const onClick = (ref) => {
    ref.target.scrollIntoView({ behaviour: "smooth", block: "center" });
  };
  return (
    <div className="px-2 pb-2 text-black bg-blue-400 dark:bg-mine-shaft-500 dark:text-gray-200 ">
      <div className="py-1 font-semibold "></div>
      <div className="flex space-x-2 overflow-auto ">
        {categoryFind && (
          <NavLink
            onClick={onClick}
            to={{
              pathname: `/products/${categoryFind.slug}-${categoryFind.id}/all`,
              search: "?page=0",
            }}
            className={categoryClass}
          >
            {"All"}
          </NavLink>
        )}
        {categoryFind &&
          categoryFind.subCat.map((a, i) => {
            let className =
              "flex-shrink-0 px-2 py-1 tracking-wider bg-white rounded-md dark:bg-black";
            if (a.id === +subCategory) {
              className =
                "flex-shrink-0 px-2 py-1 font-bold tracking-wider bg-blue-800 rounded-md dark:bg-blue-400";
            }
            return (
              <NavLink
                ref={ref.current[i]}
                onClick={onClick}
                to={{
                  pathname: `/products/${categoryFind.slug}-${categoryFind.id}/${a.slug}-${a.id}`,
                  search: "?page=0",
                }}
                key={i}
                className={className}
              >
                {a.name}
              </NavLink>
            );
          })}
      </div>
    </div>
  );
};

export default SubCategoryHeader;
