import React, { useState } from "react";
import { useHistory, Link } from "react-router-dom";
import HeaderCart from "./headerCart";
import useSearch from "../useSearch";
import Drawer from "./Drawer";
import string_to_slug from "../utils/slug";
import { SkeletonLines } from "../utils/SkeletonComponent";
function Header() {
  const [toggle, setToggle] = useState(false);
  const [open, setOpen] = useState(false);
  const onClick = () => {
    setToggle((t) => !t);
  };
  let history = useHistory();
  const [value, setValue] = useState("");
  const { response, loading } = useSearch(value);
  const Close = () => {
    setValue("");
    setOpen(false);
  };
  const onChange = (e) => {
    if (e.target.value.length > 0) {
      setOpen(true);
      setValue(e.target.value);
    } else {
      setValue("");
      setOpen(false);
    }
  };

  return (
    <>
      <Drawer toggle={toggle} onToggle={onClick}></Drawer>

      <div className="sticky top-0 z-20 px-4 bg-red-200 dark:bg-gradient-to-br bg-gradient-to-r from-green-400 to-blue-500 dark:from-lightBlue-500 dark:to-blue-900 ">
        <div className="flex items-center justify-between px-2 py-2 ">
          <div className="flex items-center justify-center">
            <button className=" focus:shadow" onClick={onClick}>
              <svg
                width="20"
                height="20"
                xmlns="http://www.w3.org/2000/svg"
                fillRule="evenodd"
                clipRule="evenodd"
              >
                <path
                  d="M24 18v1h-24v-1h24zm0-6v1h-24v-1h24zm0-6v1h-24v-1h24z"
                  fill="#1040e2"
                />
                <path d="M24 19h-24v-1h24v1zm0-6h-24v-1h24v1zm0-6h-24v-1h24v1z" />
              </svg>
            </button>
          </div>
          <Link to="/">
            <div className="flex ">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="30"
                height="30"
                viewBox="0 0 24 24"
              >
                <path
                  d="M13.958 10.09c0 1.232.029 2.256-.591 3.351-.502.891-1.301 1.438-2.186 1.438-1.214 0-1.922-.924-1.922-2.292 0-2.692 2.415-3.182 4.7-3.182v.685zm3.186 7.705c-.209.189-.512.201-.745.074-1.052-.872-1.238-1.276-1.814-2.106-1.734 1.767-2.962 2.297-5.209 2.297-2.66 0-4.731-1.641-4.731-4.925 0-2.565 1.391-4.309 3.37-5.164 1.715-.754 4.11-.891 5.942-1.095v-.41c0-.753.06-1.642-.383-2.294-.385-.579-1.124-.82-1.775-.82-1.205 0-2.277.618-2.54 1.897-.054.285-.261.567-.549.582l-3.061-.333c-.259-.056-.548-.266-.472-.66.704-3.716 4.06-4.838 7.066-4.838 1.537 0 3.547.41 4.758 1.574 1.538 1.436 1.392 3.352 1.392 5.438v4.923c0 1.481.616 2.13 1.192 2.929.204.287.247.63-.01.839-.647.541-1.794 1.537-2.423 2.099l-.008-.007zm3.559 1.988c-2.748 1.472-5.735 2.181-8.453 2.181-4.027 0-7.927-1.393-11.081-3.706-.277-.202-.481.154-.251.416 2.925 3.326 6.786 5.326 11.076 5.326 3.061 0 6.614-1.214 9.066-3.494.406-.377.058-.945-.357-.723zm.67 2.216c-.091.227.104.32.31.147 1.339-1.12 1.685-3.466 1.411-3.804-.272-.336-2.612-.626-4.04.377-.22.154-.182.367.062.337.805-.096 2.595-.312 2.913.098.319.41-.355 2.094-.656 2.845z"
                  fillRule="evenodd"
                  clipRule="evenodd"
                />
              </svg>
            </div>
          </Link>

          <Link to="/cart">
            <HeaderCart></HeaderCart>
          </Link>
        </div>
      </div>
      <div className="relative flex flex-col items-center justify-center mt-2 text-black">
        <div className="relative">
          <span className="absolute inset-y-0 flex items-center pl-2">
            <svg
              className="absolute "
              xmlns="http://www.w3.org/2000/svg"
              width="16"
              height="16"
              viewBox="0 0 24 24"
            >
              <path d="M23.822 20.88l-6.353-6.354c.93-1.465 1.467-3.2 1.467-5.059.001-5.219-4.247-9.467-9.468-9.467s-9.468 4.248-9.468 9.468c0 5.221 4.247 9.469 9.468 9.469 1.768 0 3.421-.487 4.839-1.333l6.396 6.396 3.119-3.12zm-20.294-11.412c0-3.273 2.665-5.938 5.939-5.938 3.275 0 5.94 2.664 5.94 5.938 0 3.275-2.665 5.939-5.94 5.939-3.274 0-5.939-2.664-5.939-5.939z" />
            </svg>
          </span>

          <input
            onChange={onChange}
            value={value}
            type="text"
            placeholder="Search Our Products"
            className="w-64 pl-8 pr-8 transition-all duration-100 ease-in-out rounded dark:bg-gray-700 dark:text-white focus:shadow-outline focus:w-80 focus:bg-gray-100 "
          />
          {open && (
            <span className="absolute inset-y-0 right-0 flex items-center pr-2">
              <svg
                onClick={Close}
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                viewBox="0 0 24 24"
              >
                <path d="M24 20.188l-8.315-8.209 8.2-8.282-3.697-3.697-8.212 8.318-8.31-8.203-3.666 3.666 8.321 8.24-8.206 8.313 3.666 3.666 8.237-8.318 8.285 8.203z" />
              </svg>
            </span>
          )}
        </div>
        {open && response.length > 0 && (
          <div className="absolute z-20 flex flex-col overflow-y-auto bg-gray-100 border border-orange-300 divide-y-2 rounded-md dark:bg-gray-800 dark:text-white top-12 w-80 overscroll-contain ">
            {response.map((i) => {
              return (
                <div
                  className="px-4 py-2 "
                  key={i.id}
                  onClick={() => {
                    Close();
                    history.push(
                      `/product-suggestion/${string_to_slug(i.name)}`
                    );
                  }}
                >
                  {i.name}
                </div>
              );
            })}
          </div>
        )}
        {open && loading && (
          <div className="absolute z-20 flex flex-col overflow-y-auto bg-gray-100 border border-orange-300 divide-y-2 rounded-md dark:bg-gray-800 dark:text-white top-12 w-80 overscroll-contain ">
            <SkeletonLines></SkeletonLines>
            <SkeletonLines></SkeletonLines>
            <SkeletonLines></SkeletonLines>
            <SkeletonLines></SkeletonLines>
            <SkeletonLines></SkeletonLines>
          </div>
        )}
        {open && response.length === 0 && !loading && (
          <div className="absolute z-20 flex-col overflow-y-auto bg-gray-100 border border-orange-300 divide-y-2 rounded-md lex top-12 w-80 overscroll-contain ">
            No Responses
          </div>
        )}
      </div>
    </>
  );
}

export default Header;
