import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import * as actions from "../store/actions/index";
import { toast } from "react-toastify";
import Axios from "axios";
Axios.defaults.withCredentials = true;

const CartPageButton = ({ product, sku, quantity }) => {
  const [state, setState] = useState({
    loading: false,

    increment: false,
    decrement: false,
  });
  const decrementProduct = () => {
    if (quantity === 1) {
      // toast("Removed From Cart", {
      //   position: "top-center",
      //   autoClose: 2000,
      //   type: "info",
      //   hideProgressBar: false,
      //   closeOnClick: true,
      // });
      dispatch(actions.decrementProduct(product.id, sku.id));
    } else {
      // toast("-1 Quantity", {
      //   position: "top-center",
      //   autoClose: 2000,
      //   type: "info",
      //   hideProgressBar: false,
      //   closeOnClick: true,
      // });
      dispatch(actions.decrementProduct(product.id, sku.id));
    }
  };
  const incrementProduct = () => {
    // toast("+1 Quantity", {
    //   position: "top-center",
    //   autoClose: 2000,
    //   type: "info",
    //   hideProgressBar: false,
    //   closeOnClick: true,
    // });
    dispatch(actions.incrementProduct(product.id, sku.id));
  };
  useEffect(() => {
    let mounted = true;
    const request = async () => {
      if (state.loading && state.increment && mounted) {
        try {
          await Axios.post(
            "https://frozen-reef-15026.herokuapp.com/api/increment-cart",
            {
              sku_id: sku.id,
              product_id: product.id,
              cart_id: 1,
              quantity: 1,
            }
          );
          incrementProduct();
        } catch (err) {
          if (
            err.response.data.data &&
            err.response.data.data.constraint === "greater than zero"
          ) {
            toast("Out Of Stock", {
              position: "top-center",
              autoClose: 2000,
              type: "info",
              hideProgressBar: false,
              closeOnClick: true,
            });
          } else
            toast("Error. Try Again Later", {
              position: "top-center",
              autoClose: 2000,
              type: "error",
              hideProgressBar: false,
              closeOnClick: true,
            });
        }
        setState((e) => ({ ...e, loading: false, increment: false }));
      }
      if (state.loading && state.decrement && mounted) {
        try {
          await Axios.post(
            "https://frozen-reef-15026.herokuapp.com/api/decrement-cart",
            {
              sku_id: sku.id,
              product_id: product.id,
              cart_id: 1,
              quantity: 1,
            }
          );
          decrementProduct();
        } catch (err) {
          if (err.response.data && err.response.data.message) {
            toast(err.response.data.message, {
              position: "top-center",
              autoClose: 2000,
              type: "info",
              hideProgressBar: false,
              closeOnClick: true,
            });
          } else
            toast("Error. Try Again Later", {
              position: "top-center",
              autoClose: 2000,
              type: "error",
              hideProgressBar: false,
              closeOnClick: true,
            });
        }
        setState((e) => ({ ...e, loading: false, decrement: false }));
      }
    };
    request();
    return () => (mounted = false);
  }, [state]);
  const dispatch = useDispatch();
  useEffect(() => {
    return () => {
      toast.dismiss();
    };
  });

  //   const quantity = useSelector((state) => {
  //     if (!state.cartBuilder.cart.length) {
  //       return 0;
  //     } else {
  //       if (sku) {
  //         let q = state.cartBuilder.cart.findIndex((i) => i.sku_id === sku.id);
  //         if (q === -1) {
  //           return 0;
  //         } else {
  //           return state.cartBuilder.cart[q].quantity;
  //         }
  //       } else {
  //         return;
  //       }
  //     }
  //   });
  return (
    <>
      {" "}
      {state.loading ? (
        <>
          <button className="inline-flex items-center px-3 py-2 mt-4 text-base leading-6 text-white transition duration-150 ease-in-out bg-indigo-500 border border-transparent rounded-md shadow-sm focus:outline-none focus:ring-indigo-300 hover:bg-indigo-300 active:bg-indigo-700 ">
            <svg
              class="animate-spin -ml-1 mr-3 h-5 w-5 text-white"
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
            >
              <circle
                class="opacity-25"
                cx="12"
                cy="12"
                r="10"
                stroke="currentColor"
                stroke-width="4"
              ></circle>
              <path
                class="opacity-75"
                fill="currentColor"
                d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
              ></path>
            </svg>
            Processing
          </button>
        </>
      ) : (
        <div className="flex items-center justify-between h-8 border border-gray-400 rounded-md ">
          <button
            onClick={() => {
              setState((e) => ({ ...e, loading: true, decrement: true }));
            }}
            className="py-1 leading-none text-gray-700 bg-gray-400 "
          >
            <svg
              xmlns="http://www.w3.org/200s0/svg"
              className="w-8 h-6 px-2"
              viewBox="0 0 24 24"
              fill="currentColor"
            >
              <path d="M0 10h24v4h-24z" />
            </svg>
          </button>
          <div className="px-3 py-1 text-blue-500 ">{quantity}</div>
          <div
            className="py-1 leading-none text-gray-700 bg-gray-400 "
            onClick={() => {
              if (quantity === 5) {
                toast("Max Quantity", {
                  position: "top-center",
                  autoClose: 2000,
                  type: "info",
                  hideProgressBar: false,
                  closeOnClick: true,
                });
              } else
                setState((e) => ({ ...e, loading: true, increment: true }));
            }}
          >
            <svg
              // onClick={incrementProduct}
              className="w-8 h-6 px-2"
              fill="currentColor"
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth="2"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path d="M12 4v16m8-8H4"></path>
            </svg>
          </div>
        </div>
      )}
    </>
  );
};
export default CartPageButton;
