import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import CartProduct from "../components/cartProduct";
import { useHistory } from "react-router-dom";
import * as actions from "../store/actions/index";

import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

import { SkeletonComponent } from "../utils/SkeletonComponent";
import ClearCart from "../components/clearCart";
function CartPage(props) {
  let history = useHistory();

  const dispatch = useDispatch();
  console.log(history);
  const onClick = () => {
    dispatch(actions.purchaseInit());
    history.push({ pathname: "/place_order" });
  };

  useEffect(() => {
    dispatch(actions.getCart());
  }, [dispatch]);
  const state = useSelector((state) => state.cartBuilder.cart);
  const loading = useSelector((state) => state.cartBuilder.loading);
  const render = () => {
    if (state && state.length > 0 && !loading) {
      const total_price = () => {
        let amount = 0;
        state.map((i) => (amount += i.skuId.sp * i.quantity));
        return amount.toFixed(2);
      };
      const total_initialPrice = () => {
        let amount = 0;
        state.map((i) => (amount += i.skuId.mrp * i.quantity));
        return amount.toFixed(2);
      };

      const saved = () => (total_initialPrice() - total_price()).toFixed(2);

      return (
        <div className="flex flex-col h-full bg-gray-100 ">
          <ToastContainer
            position="top-left"
            autoClose={1500}
            hideProgressBar={false}
            newestOnTop={false}
            closeOnClick
            rtl={false}
            pauseOnFocusLoss
            draggable
            pauseOnHover
          />

          <div className="flex-1 h-full mx-2 overflow-auto">
            <div className="flex py-2 space-x-1 ">
              <div>SubTotal ({state.length} items): </div>
              <span className="font-bold text-red-600">
                &#8377; {total_price()}
              </span>
              <div className="flex px-1 ml-1 space-x-1 text-white bg-red-400">
                <span> Saved &#8377; {saved()}</span>
              </div>
            </div>
            <button
              onClick={onClick}
              className="inline-block w-full py-2 font-bold text-center text-white bg-yellow-500 rounded-md "
            >
              Proceed To Buy
            </button>

            {state.map((i) => (
              <CartProduct
                product={i.productId}
                key={i.id}
                quantity={i.quantity}
                sku={i.skuId}
              ></CartProduct>
            ))}
          </div>
        </div>
      );
    } else if (state && state.length === 0) {
      return (
        <div className="flex flex-col items-center justify-center flex-1 h-screen overflow-auto">
          <div className="py-4 text-xl text-gray-600">Your cart is Empty</div>
        </div>
      );
    } else {
      return (
        <div className="divide-y divide-gray-200 ">
          <div className="px-4 py-2">
            <div className="animate-pulse">
              <SkeletonComponent type="long"></SkeletonComponent>
            </div>
          </div>
          <div className="px-4 py-2">
            <div
              className="animate-pulse"
              style={{
                animationFillMode: "backwards",
                animationDelay: "150ms",
              }}
            >
              <SkeletonComponent type="short"></SkeletonComponent>
            </div>
          </div>
          <div className="px-4 py-2">
            <div
              className="animate-pulse"
              style={{
                animationFillMode: "backwards",
                animationDelay: "300ms",
              }}
            >
              <SkeletonComponent type="long"></SkeletonComponent>
            </div>
          </div>
          <div className="px-4 py-2">
            <div
              className="animate-pulse"
              style={{
                animationFillMode: "backwards",
                animationDelay: "450ms",
              }}
            >
              <SkeletonComponent type="short"></SkeletonComponent>
            </div>
          </div>
        </div>
      );
    }
  };

  return (
    <div className="flex flex-col w-full h-full bg-gray-500">
      <div className="flex flex-col flex-1 h-full bg-gray-100">
        <div className="sticky top-0 z-10 flex justify-between py-2 text-white bg-red-500">
          <div className="flex items-center justify-center">
            <div className="px-2 " onClick={() => history.goBack()}>
              <svg
                fill="currentColor"
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                viewBox="0 0 24 24"
              >
                <path d="M16.67 0l2.83 2.829-9.339 9.175 9.339 9.167-2.83 2.829-12.17-11.996z" />
              </svg>
            </div>
            <div className="font-bold">Cart</div>
          </div>
          {state.length > 0 && <ClearCart />}
        </div>
        <div className="px-3 py-2 mx-2 my-2 bg-gray-200 rounded">
          Products are moved into your wishlist after 2 Hours of Inactivity
        </div>
        {render()}
      </div>
    </div>
  );
}

export default CartPage;
