import React from "react";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import Axios from "axios";
import { useHistory } from "react-router-dom";
import Spinner from "../utils/Spinner/spinner";
import { CustomInput } from "../utils/CustomInput";
Axios.defaults.withCredentials = true;

function EditProductType(props) {
  let id, type;
  if (props.location.state) {
    id = props.location.state.id;

    type = props.location.state.type;
  }
  let history = useHistory();

  let initialValues = {
    sku: {
      mrp: type.mrp,
      discount: type.discount,
      sku: type.sku,
      stock: type.stock,
      product_id: id,
      id: type.id,

      sp: type.sp,
    },
  };
  const validationSchema = Yup.object().shape({
    sku: Yup.object().shape({
      product_id: Yup.number().required(),
      mrp: Yup.number().required(),
      discount: Yup.number().required(),
      stock: Yup.number().required(),
      sku: Yup.string().required(),
      sp: Yup.number().required(),
    }),
  });

  async function _handleSubmit(values, actions) {
    try {
      actions.setSubmitting(true);
      const { data } = await Axios.patch(
        `https://frozen-reef-15026.herokuapp.com/api/sku`,
        values
      );
      console.log(data);
      actions.setSubmitting(false);
      actions.resetForm();
      history.push(`/product/${data[0].product_id}`);
    } catch (err) {
      console.log(JSON.stringify(err.response, 2, null));
      if (err.response.status === 409) {
        actions.setStatus({ error: err.response.data.details });
      } else if (err.response.status === 422) {
        actions.setStatus({ error: err.response.message });
      } else {
        actions.setStatus({ error: "Server Error" });
      }
      actions.setSubmitting(false);
    }
  }

  const parseAndHandleChange = (value, setFieldValue, id) => {
    const parsed = parseFloat(value, 10);
    setFieldValue(id, parsed);
  };
  return (
    <div>
      <div className="flex flex-col h-screen ">
        <div className="flex py-2 text-white bg-red-500 ">
          <div className="flex items-center justify-center">
            <div className="px-2 " onClick={() => history.goBack()}>
              <svg
                fill="currentColor"
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                viewBox="0 0 24 24"
              >
                <path d="M16.67 0l2.83 2.829-9.339 9.175 9.339 9.167-2.83 2.829-12.17-11.996z" />
              </svg>
            </div>
            <div className="font-bold">Edit Product Sku</div>
          </div>
        </div>
        <div className="flex flex-col flex-1 pt-4 mx-8 ">
          <Formik
            initialValues={initialValues}
            validationSchema={validationSchema}
            onSubmit={(values, actions) => _handleSubmit(values, actions)}
          >
            {({
              values,
              handleSubmit,
              handleChange,
              setFieldValue,
              status,
              isSubmitting,
              validateForm,
            }) => {
              return (
                <Form className="flex flex-col flex-1 ">
                  <div className="flex flex-col justify-between flex-1 ">
                    <div className="flex flex-col flex-1 pb-10 mx-4 space-y-2 ">
                      <CustomInput
                        label="Product Sku"
                        name="sku.sku"
                        type="text"
                        onChange={handleChange}
                        value={values.sku.sku}
                        placeholder="Product Sku"
                      ></CustomInput>
                      <CustomInput
                        label="Product Mrp"
                        name="sku.mrp"
                        type="number"
                        onChange={handleChange}
                        value={values.sku.mrp}
                        placeholder="Product Mrp"
                      ></CustomInput>
                      <CustomInput
                        label="Product Discount"
                        name="sku.discount"
                        type="number"
                        onChange={(event) =>
                          parseAndHandleChange(
                            event.target.value,
                            setFieldValue,
                            "sku.discount"
                          )
                        }
                        value={values.sku.discount}
                        placeholder="Discount"
                      ></CustomInput>
                      <CustomInput
                        label="Product Selling Price"
                        name="sku.sp"
                        type="number"
                        onChange={(event) =>
                          parseAndHandleChange(
                            event.target.value,
                            setFieldValue,
                            "sku.sp"
                          )
                        }
                        value={values.sku.sp}
                        placeholder="Selling Price"
                      ></CustomInput>
                      <CustomInput
                        label="Product Stock"
                        name="sku.stock"
                        type="number"
                        onChange={(event) =>
                          parseAndHandleChange(
                            event.target.value,
                            setFieldValue,
                            "sku.stock"
                          )
                        }
                        value={values.sku.stock}
                        placeholder="Product Stock"
                      ></CustomInput>
                    </div>

                    {status && status.error ? (
                      <div className="px-2 py-1 text-white bg-red-400">
                        {status.error}
                      </div>
                    ) : null}
                    <div className="flex justify-end pb-16">
                      {isSubmitting ? (
                        <div className="px-8 py-4 mr-2 text-white bg-gray-300 rounded ">
                          {" "}
                          <Spinner width={15} height={15}></Spinner>
                        </div>
                      ) : (
                        <button
                          type="submit"
                          onClick={() =>
                            validateForm().then((e) => {
                              console.log(e);
                              if (!e) {
                                return handleSubmit(values);
                              }
                              console.log(values);
                              return;
                            })
                          }
                          className="px-4 py-4 mr-2 text-white bg-green-600 rounded "
                        >
                          Edit
                        </button>
                      )}
                    </div>
                  </div>
                </Form>
              );
            }}
          </Formik>
        </div>
      </div>
    </div>
  );
}

export default EditProductType;
