import { useSelector } from "react-redux";
import React from "react";
import { Redirect, Route } from "react-router-dom";

function PrivateRoute({ children, ...rest }) {
  const state = useSelector((state) => state.authBuilder.isSignedIn);
  console.log(state);

  if (state === null) {
    return null;
  } else {
    return (
      <Route
        {...rest}
        render={({ location }) =>
          state ? (
            children
          ) : (
            <Redirect
              to={{
                pathname: "/login",
                state: { from: location },
              }}
            />
          )
        }
      />
    );
  }
}
export default PrivateRoute;
