import * as actionTypes from "../actions/actionTypes";
const initial_state = {
  userId: null,
  isSignedIn: false,
  phoneNumber: null,
  loading: false,
  address: null,
  defaultAddress: null,
  isAdmin: false,
};

export default (state = initial_state, action) => {
  switch (action.type) {
    case actionTypes.SIGN_IN:
      return {
        ...state,
        isSignedIn: true,
        isAdmin: action.payload.isAdmin,
        userId: action.payload.response.id,
        phoneNumber: action.payload.response.phoneNumber,
        address: action.payload.response.address,
        loading: false,
      };
    case actionTypes.SIGN_OUT:
      return {
        ...state,
        isSignedIn: false,
        phoneNumber: null,
        address: null,
        userId: null,
        isAdmin: false,
        loading: false,
      };
    case actionTypes.AUTH_LOADING:
      return { ...state, loading: true };
    case actionTypes.CURRENT_USER:
      console.log(action.payload);
      return {
        ...state,
        isSignedIn: true,
        loading: false,
        userId: action.payload.response.id,
        phoneNumber: action.payload.response.phoneNumber,
        address: action.payload.response.address,
        isAdmin: action.payload.isAdmin,
      };
    case actionTypes.SET_ADDRESS:
      console.log(action.payload);
      return {
        ...state,
        address: [...action.payload],
        loading: false,
      };
    case actionTypes.FETCH_ADDRESS_LOADING:
      return {
        ...state,
        loading: true,
      };
    case actionTypes.ADD_ADDRESS:
      return {
        ...state,
        address: [...state.address, action.payload],
      };
    case actionTypes.DELETE_ADDRESS:
      const id = action.payload;
      const addresses = state.address.filter((i) => i.id !== id);
      return {
        ...state,
        loading: false,

        address: addresses,
      };
    default:
      return state;
  }
};
