import React, { useCallback, useEffect } from "react";
import * as actions from "./store/actions";
import {
  Switch,
  Route,
  BrowserRouter as Router,
  useHistory,
} from "react-router-dom";
import HomePage from "./containers/HomePage";
import CartPage from "./containers/CartPage";
import SubCategory from "./containers/SubCategory";
import { useDispatch } from "react-redux";
import Infinite from "./components/Infinite";
import PlaceOrder from "./containers/PlaceOrder";
import OrderConfirm from "./containers/OrderConfirm";
import LoginPage from "./containers/LoginPage";
import AddProduct from "./components/addProduct";
import AddCategory from "./components/addCategory";
import AddProductCategory from "./components/addProductCategory";
import Product from "./containers/Product";
import SearchProduct from "./containers/SearchProduct";
import EditProductType from "./components/editProductTypes";
import ProtectedRoute from "./ProtectedRoute";
import AddAddress from "./components/addAddress";
import Profile from "./containers/Profile";
import Orders from "./containers/Orders";
import Order from "./containers/Order";
import EditAddress from "./components/editAddress";

import ErrorPage from "./containers/ErrorPage";

import ProductContainer from "./components/ProductContainer";
import AddProductType from "./components/addProductTypes";
import WishListPage from "./containers/WishListPage";

const App = () => {
  const theme = localStorage.getItem("theme");
  if (!theme) {
    localStorage.setItem("theme", "light");
  } else if (theme === "dark") {
    document.body.classList.add("dark");
  } else document.body.classList.remove("dark");
  const dispatch = useDispatch();

  const onInit = useCallback(() => {
    dispatch(actions.initCategory());
    dispatch(actions.currentUser());
    dispatch(actions.initCart());
  }, [dispatch]);
  useEffect(() => {
    onInit();
  }, [onInit, dispatch]);
  let history = useHistory();
  console.log(history);
  return (
    <>
      <Router>
        <Switch>
          <ProtectedRoute path="/place_order" exact>
            <PlaceOrder></PlaceOrder>{" "}
          </ProtectedRoute>
          <ProtectedRoute path="/orders" exact>
            <Orders></Orders>{" "}
          </ProtectedRoute>
          <ProtectedRoute path="/order">
            <Order></Order>{" "}
          </ProtectedRoute>

          <ProtectedRoute path="/order_confirm" exact>
            <OrderConfirm></OrderConfirm>
          </ProtectedRoute>
          <ProtectedRoute path="/profile" exact>
            <Profile />
          </ProtectedRoute>
          <ProtectedRoute path="/add_address" exact>
            <AddAddress></AddAddress>
          </ProtectedRoute>
          <ProtectedRoute path="/edit_address" exact>
            <EditAddress></EditAddress>
          </ProtectedRoute>
          <ProtectedRoute path="/wishlist" exact>
            <WishListPage></WishListPage>
          </ProtectedRoute>
          <Route path="/login" exact component={LoginPage}></Route>
          <Route
            path="/product-suggestion/:name"
            exact
            component={SearchProduct}
          ></Route>
          <Route path="/product/:id" exact component={Product}></Route>
          <Route path="/add-product" exact component={AddProduct}></Route>
          <Route path="/products" exact>
            <ProductContainer />
          </Route>
          <Route
            path="/products/:category/:subCategory"
            exact
            component={SubCategory}
          ></Route>
          <Route
            path="/add-product-category"
            exact
            component={AddProductCategory}
          ></Route>
          <Route path="/edit-product-type" component={EditProductType}></Route>
          <Route path="/edit-product/:id" component={AddProduct}></Route>
          <Route path="/add-product-type/" component={AddProductType}></Route>
          <Route path="/add-category" exact component={AddCategory}></Route>
          <Route path="/infinite" component={Infinite}></Route>
          <Route path="/cart" exact>
            <CartPage></CartPage>
          </Route>

          <Route path="/" exact component={HomePage}></Route>
          <Route
            path="/products/:category"
            exact
            component={SubCategory}
          ></Route>
          <Route component={ErrorPage}></Route>
        </Switch>
      </Router>
    </>
  );
};

export default App;
