import Axios, { CancelToken } from "axios";
import React from "react";
import { useQuery } from "react-query";
import { useHistory } from "react-router-dom";
import WishListProduct from "../components/WishListProduct";
import { SkeletonComponent } from "../utils/SkeletonComponent";
import ErrorPage from "./ErrorPage";
Axios.defaults.withCredentials = true;

export default function () {
  const { data, status } = useQuery(
    "wishlist-query",
    () => {
      const source = CancelToken.source();

      const promise = Axios.get(
        "https://frozen-reef-15026.herokuapp.com/api/wishlist",
        {
          cancelToken: source.token,
        }
      );

      // Cancel the request if React Query calls the `promise.cancel` method
      promise.cancel = () => {
        source.cancel("Query was cancelled by React Query");
      };

      return promise;
    },
    {
      refetchOnMount: false,
      refetchOnWindowFocus: false,
      cacheTime: 3000,
      staleTime: 60000,
    }
  );
  if (status === "loading")
    return (
      <div className="min-h-screen divide-y divide-gray-200 ">
        <div className="px-4 py-2">
          <div className="animate-pulse">
            <SkeletonComponent type="long"></SkeletonComponent>
          </div>
        </div>
        <div className="px-4 py-2">
          <div
            className="animate-pulse"
            style={{ animationFillMode: "backwards", animationDelay: "150ms" }}
          >
            <SkeletonComponent type="short"></SkeletonComponent>
          </div>
        </div>
        <div className="px-4 py-2">
          <div
            className="animate-pulse"
            style={{ animationFillMode: "backwards", animationDelay: "300ms" }}
          >
            <SkeletonComponent type="long"></SkeletonComponent>
          </div>
        </div>
        <div className="px-4 py-2">
          <div
            className="animate-pulse"
            style={{ animationFillMode: "backwards", animationDelay: "450ms" }}
          >
            <SkeletonComponent type="short"></SkeletonComponent>
          </div>
        </div>
      </div>
    );
  if (status === "error") return <ErrorPage />;
  if (data.length === 0) {
    return (
      <div className="flex flex-col w-full h-full bg-gray-500">
        <div className="flex flex-col flex-1 h-full bg-gray-100">
          <div className="sticky top-0 z-10 flex justify-between py-2 text-white bg-red-500">
            <div className="flex items-center justify-center">
              <div className="px-2 " onClick={() => history.goBack()}>
                <svg
                  fill="currentColor"
                  xmlns="http://www.w3.org/2000/svg"
                  width="16"
                  height="16"
                  viewBox="0 0 24 24"
                >
                  <path d="M16.67 0l2.83 2.829-9.339 9.175 9.339 9.167-2.83 2.829-12.17-11.996z" />
                </svg>
              </div>
              <div className="font-bold">WishList</div>
            </div>
          </div>
        </div>
      </div>
    );
  }
  let history = useHistory();
  return (
    <div className="flex flex-col w-full h-full bg-gray-500">
      <div className="flex flex-col flex-1 h-full bg-gray-100">
        <div className="sticky top-0 z-10 flex justify-between py-2 text-white bg-red-500">
          <div className="flex items-center justify-center">
            <div className="px-2 " onClick={() => history.goBack()}>
              <svg
                fill="currentColor"
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                viewBox="0 0 24 24"
              >
                <path d="M16.67 0l2.83 2.829-9.339 9.175 9.339 9.167-2.83 2.829-12.17-11.996z" />
              </svg>
            </div>
            <div className="font-bold">WishList</div>
          </div>
        </div>
        <div className="flex-1 h-full component dark:bg-mine-shaft-500">
          {data.data.map((i, index) => {
            //  if (products.length === index + 1) {
            //   return (
            //     <div key={index}>
            //       <div ref={lastProductRef}></div>
            //       <Product key={index} product={i}></Product>
            //       {loading ? (
            //         <div className="flex items-center justify-center pt-3">
            //           <Spinner></Spinner>
            //         </div>
            //       ) : null}
            //     </div>
            //   );
            // }
            return (
              <WishListProduct
                sku={i.skuId}
                product={i.productId}
              ></WishListProduct>
            );
          })}{" "}
        </div>
      </div>
    </div>
  );
}
