import React, { useState } from "react";
import ProductFilter from "./ProductFilter";
import BrandFilter from "./BrandFilter";

function Filters({ showFilters, onDrawerClick, openBackDrop }) {
  const [toggleBrandFilter, setToggleBrandFilter] = useState(false);

  const [toggleProductFilter, setToggleProductFilter] = useState(false);

  // const backdrop = () => {
  //   if (showFilters || toggleBrandFilter || toggleProductFilter) {
  //     return (
  //       <BackDrop
  //         onClick={() => {
  //           openBackDrop(false);
  //           setshowProductFilter(false);
  //           setToggleBrandFilter(false);
  //         }}
  //       ></BackDrop>
  //     );
  //   } else {
  //     return null;
  //   }
  // };
  return (
    <>
      <div className="flex items-center justify-between border-b border-gray-400 divide-x dark:bg-mine-shaft-700 dark:text-gray-300">
        <div
          className="flex items-center justify-center w-full py-2 space-x-4 font-mono font-semibold active:bg-blue-300"
          onClick={() => {
            // openBackDrop(true);
            return setToggleProductFilter(true);
          }}
        >
          <span className="">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
              className="w-6 h-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M3 4h13M3 8h9m-9 4h9m5-4v12m0 0l-4-4m4 4l4-4"
              />
            </svg>
          </span>
          <span className="flex justify-center ">Sort</span>
        </div>
        <div className="flex items-center justify-center w-full py-2 space-x-4 font-mono font-semibold focus:bg-gray-300 active:bg-blue-300">
          <span className="">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
              className="w-6 h-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M3 4a1 1 0 011-1h16a1 1 0 011 1v2.586a1 1 0 01-.293.707l-6.414 6.414a1 1 0 00-.293.707V17l-4 4v-6.586a1 1 0 00-.293-.707L3.293 7.293A1 1 0 013 6.586V4z"
              />
            </svg>
          </span>
          <span
            className="flex justify-center "
            onClick={() => {
              return setToggleBrandFilter(true);
            }}
          >
            Brand Filter
          </span>
        </div>
      </div>
      {/* {backdrop()} */}

      {toggleProductFilter && (
        <ProductFilter
          toggleProductFilter={toggleProductFilter}
          setToggleProductFilter={() => setToggleProductFilter((e) => !e)}
        ></ProductFilter>
      )}
      {toggleBrandFilter && (
        <BrandFilter
          toggleBrandFilter={toggleBrandFilter}
          setToggleBrandFilter={() => setToggleBrandFilter(false)}
        ></BrandFilter>
      )}
    </>
  );
}

export default Filters;
