import React from "react";
import { Formik, Form } from "formik";
import Axios from "axios";
import * as Yup from "yup";
import string_to_slug from "../utils/slug";
import { CustomInput } from "../utils/CustomInput";
import { useHistory } from "react-router-dom";
const AddCategory = () => {
  let history = useHistory();

  async function _handleSubmit(values, actions) {
    let category = values.category;
    console.log(category);

    let slug = string_to_slug(category.name);
    category.slug = slug;

    const body = {
      category: {
        name: category.name,
        slug: category.slug,
        image_url: category.url,
      },
    };
    await Axios.post(
      "https://frozen-reef-15026.herokuapp.com/api/category",
      body
    );
    history.push("/");
  }
  // const CustomTextInput = ({ label, ...props }) => {
  //   const [field, meta] = useField(props);
  //   return (
  //     <>
  //       <label className="text-gray-600" htmlFor={props.name}>
  //         {label}
  //       </label>
  //       <input
  //         {...field}
  //         className="w-full px-3 py-2 text-sm leading-tight text-gray-700 border rounded shadow appearance-none focus:outline-none focus:shadow-outline"
  //       />
  //       {meta.touched && meta.error ? (
  //         <div className="py-1 pl-4 text-white bg-red-400 ">{meta.error}</div>
  //       ) : null}
  //     </>
  //   );
  // };

  return (
    <div>
      <div className="flex flex-col h-screen ">
        <div className="flex py-2 text-white bg-red-500 ">
          <div className="flex items-center justify-center">
            <div className="px-2 " onClick={() => history.goBack()}>
              <svg
                fill="currentColor"
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                viewBox="0 0 24 24"
              >
                <path d="M16.67 0l2.83 2.829-9.339 9.175 9.339 9.167-2.83 2.829-12.17-11.996z" />
              </svg>
            </div>
            <div className="font-bold">Add Category</div>
          </div>
        </div>
        <div className="flex flex-col flex-1 pt-4 mx-8 ">
          <Formik
            initialValues={{
              category: {
                name: "",
                image_url: "",
              },
            }}
            validationSchema={Yup.object().shape({
              category: Yup.object().shape({
                name: Yup.string().required("Please enter Category Name"),
              }),
            })}
            onSubmit={(values, actions) => _handleSubmit(values, actions)}
          >
            {({
              values,

              handleChange,

              isSubmitting,
              validateForm,
            }) => {
              return (
                <Form className="flex flex-col flex-1 overflow-y-auto ">
                  <div className="flex flex-col justify-between flex-1 overflow-y-auto ">
                    <div className="flex flex-col flex-1 pb-10 mx-4 space-y-2 overflow-y-auto ">
                      <CustomInput
                        label="Category Name"
                        name="category.name"
                        type="text"
                        onChange={handleChange}
                        value={values.category.name}
                        placeholder="Category Name"
                      ></CustomInput>
                      <CustomInput
                        label="Image"
                        name="category.image_url"
                        type="text"
                        onChange={handleChange}
                        value={values.category.image_url}
                        placeholder="Image Url"
                      ></CustomInput>
                    </div>

                    <div className="flex justify-end py-2 mb-10">
                      <button
                        type="submit"
                        onClick={() =>
                          validateForm().then((e) => {
                            if (!e) {
                              return _handleSubmit(values);
                            }
                            return;
                          })
                        }
                        className="px-4 py-2 mr-2 text-white bg-green-600 rounded "
                      >
                        Next
                      </button>
                    </div>
                  </div>
                </Form>
              );
            }}
          </Formik>
        </div>
      </div>
    </div>
  );
};

export default AddCategory;
