import React from "react";
import Product from "./product";
import { useLocation, useHistory, Link, useParams } from "react-router-dom";
import { useQuery } from "react-query";
import Axios, { CancelToken } from "axios";
import { SkeletonComponent } from "../utils/SkeletonComponent";
const queryString = require("query-string");

function ProductContainer() {
  let location = useLocation();
  let pathname = location.pathname;

  let brandParams;
  const parsed = queryString.parse(location.search, {
    arrayFormat: "bracket",
  });
  console.log(parsed);
  if (parsed.brand) {
    brandParams = parsed.brand;
  }

  const currentPage = new URLSearchParams(location.search).get("page") || 0;
  const pagination = (order) => {
    let updatedPage = queryString.parse(location.search, {
      arrayFormat: "bracket",
    });
    console.log(updatedPage);
    let brand;
    let sort;
    order === 1
      ? (updatedPage.page = +currentPage + 1)
      : (updatedPage.page = +currentPage - 1);
    if (updatedPage.brand) {
      brand = updatedPage.brand;
    }
    if (updatedPage.sort) {
      sort = updatedPage.sort;
    }
    console.log(
      queryString.stringify(
        { page: updatedPage.page, brand: brand, sort },
        {
          arrayFormat: "bracket",
          skipEmptyString: true,
          skipNull: true,
        }
      )
    );
    return queryString.stringify(
      { page: updatedPage.page, brand: brand, sort },
      {
        arrayFormat: "bracket",
        skipEmptyString: true,
        skipNull: true,
      }
    );
  };
  const sort = new URLSearchParams(location.search).get("sort") || "popularity";

  let { category, subCategory } = useParams();
  let category_id = category.split("-").pop();
  let product_category_id =
    subCategory === "all" ? "all" : subCategory.split("-").pop();

  let history = useHistory();
  const hasMore = () => {
    if (data) {
      return (+currentPage + 1) * 10 < data.data.total;
    }
  };

  // useEffect(() => {
  //   setSearch(history.location.search);
  //   console.log(search);
  // }, [history.location.search]);

  //   let dispatch = useDispatch();
  //   const ref = useRef();
  //   let location = useLocation();
  //   const products = useSelector((state) => state.productBuilder.products);
  const { data, status } = useQuery(
    `${history.location.pathname}+${history.location.search}`,
    () => {
      const source = Axios.CancelToken.source();
      console.log(source);
      let url;
      if (product_category_id === "all") {
        url = `https://frozen-reef-15026.herokuapp.com/api/products?category_id=${category_id}&page=${+currentPage}`;
      } else {
        url = `https://frozen-reef-15026.herokuapp.com/api/products?category_id=${category_id}&product_category_id=${product_category_id}&page=${+currentPage}`;
      }

      const promise = Axios.get(url, {
        params: { brand: brandParams, sort: sort },
        cancelToken: source.token,
      });

      // Cancel the request if React Query calls the `promise.cancel` method
      promise.cancel = () => {
        console.log("cancelled");
        source.cancel("Query was cancelled by React Query");
      };

      return promise;
    },
    {
      refetchOnMount: false,
      refetchOnWindowFocus: false,
      cacheTime: 600000,
      staleTime: 60000,
    }
  );

  if (status === "loading")
    return (
      <div className="min-h-screen divide-y divide-gray-200 ">
        <div className="px-4 py-2">
          <div className="animate-pulse">
            <SkeletonComponent type="long"></SkeletonComponent>
          </div>
        </div>
        <div className="px-4 py-2">
          <div
            className="animate-pulse"
            style={{ animationFillMode: "backwards", animationDelay: "150ms" }}
          >
            <SkeletonComponent type="short"></SkeletonComponent>
          </div>
        </div>
        <div className="px-4 py-2">
          <div
            className="animate-pulse"
            style={{ animationFillMode: "backwards", animationDelay: "300ms" }}
          >
            <SkeletonComponent type="long"></SkeletonComponent>
          </div>
        </div>
        <div className="px-4 py-2">
          <div
            className="animate-pulse"
            style={{ animationFillMode: "backwards", animationDelay: "450ms" }}
          >
            <SkeletonComponent type="short"></SkeletonComponent>
          </div>
        </div>
      </div>
    );
  if (status === "error")
    return (
      <div className="divide-y divide-gray-200 ">
        <div className="px-4 py-2">
          <div className="animate-pulse">
            <SkeletonComponent type="long"></SkeletonComponent>
          </div>
        </div>
        <div className="px-4 py-2">
          <div
            className="animate-pulse"
            style={{ animationFillMode: "backwards", animationDelay: "150ms" }}
          >
            <SkeletonComponent type="short"></SkeletonComponent>
          </div>
        </div>
        <div className="px-4 py-2">
          <div
            className="animate-pulse"
            style={{ animationFillMode: "backwards", animationDelay: "300ms" }}
          >
            <SkeletonComponent type="long"></SkeletonComponent>
          </div>
        </div>
      </div>
    );

  if (data.data.results.length === 0) {
    return <div>No Products</div>;
  }
  return (
    <div className="flex-1 h-full component dark:bg-mine-shaft-500">
      {data.data.results.map((i, index) => {
        //  if (products.length === index + 1) {
        //   return (
        //     <div key={index}>
        //       <div ref={lastProductRef}></div>
        //       <Product key={index} product={i}></Product>
        //       {loading ? (
        //         <div className="flex items-center justify-center pt-3">
        //           <Spinner></Spinner>
        //         </div>
        //       ) : null}
        //     </div>
        //   );
        // }
        return <Product key={index} product={i}></Product>;
      })}

      <div className="flex items-center justify-around py-2 pagination ">
        {+currentPage === 0 ? (
          <button
            disabled
            className="flex w-8 h-8 px-2 py-2 text-black bg-gray-100 border-solid rounded-lg shadow-xs cursor-pointer "
          >
            <svg
              fill="none"
              viewBox="0 0 32 32"
              xmlns="http://www.w3.org/2000/svg"
              aria-hidden="true"
              role="presentation"
              focusable="false"
              className="block w-4 h-4 overflow-hidden stroke-current "
              style={{ strokeWidth: 4 }}
            >
              <g fill="none">
                <path d="m20 28-11.29289322-11.2928932c-.39052429-.3905243-.39052429-1.0236893 0-1.4142136l11.29289322-11.2928932"></path>
              </g>
            </svg>
          </button>
        ) : (
          <Link
            to={{
              pathname: pathname,
              search: pagination(0),
            }}
            className="flex w-8 h-8 px-2 py-2 text-black bg-gray-300 border-solid rounded-lg shadow-xs cursor-pointer "
          >
            <svg
              fill="none"
              viewBox="0 0 32 32"
              xmlns="http://www.w3.org/2000/svg"
              aria-hidden="true"
              role="presentation"
              focusable="false"
              className="block w-4 h-4 overflow-hidden stroke-current "
              style={{ strokeWidth: 4 }}
            >
              <g fill="none">
                <path d="m20 28-11.29289322-11.2928932c-.39052429-.3905243-.39052429-1.0236893 0-1.4142136l11.29289322-11.2928932"></path>
              </g>
            </svg>
          </Link>
        )}
        <span> Page No. {+currentPage + 1}</span>
        {hasMore() ? (
          <Link
            to={{
              pathname: pathname,
              search: pagination(1),
            }}
            className="flex w-8 h-8 px-2 py-2 text-black bg-gray-300 border-solid rounded-lg shadow-xs cursor-pointer "
          >
            <svg
              className="block w-4 h-4 overflow-hidden stroke-current "
              style={{ strokeWidth: 4 }}
              viewBox="0 0 32 32"
              xmlns="http://www.w3.org/2000/svg"
              aria-hidden="true"
              role="presentation"
              focusable="false"
            >
              <g fill="none">
                <path d="m12 4 11.2928932 11.2928932c.3905243.3905243.3905243 1.0236893 0 1.4142136l-11.2928932 11.2928932"></path>
              </g>
            </svg>
          </Link>
        ) : (
          <button
            disabled
            className="flex w-8 h-8 px-2 py-2 text-black bg-gray-100 border-solid rounded-lg shadow-xs cursor-pointer "
          >
            <svg
              className="block w-4 h-4 overflow-hidden stroke-current "
              style={{ strokeWidth: 4 }}
              viewBox="0 0 32 32"
              xmlns="http://www.w3.org/2000/svg"
              aria-hidden="true"
              role="presentation"
              focusable="false"
            >
              <g fill="none">
                <path d="m12 4 11.2928932 11.2928932c.3905243.3905243.3905243 1.0236893 0 1.4142136l-11.2928932 11.2928932"></path>
              </g>
            </svg>
          </button>
        )}
      </div>
    </div>
  );
}

export default ProductContainer;
