import React from "react";

import { Formik, useField, Form } from "formik";
import * as Yup from "yup";
import { useHistory, useLocation } from "react-router-dom";
import Select from "react-select";
import Axios from "axios";
Axios.defaults.withCredentials = true;

function EditAddress() {
  let history = useHistory();
  let location = useLocation();
  //   async function _submitForm(values, actions) {
  //     await _sleep(1000);
  //     alert(JSON.stringify(values, null, 2));
  //     history.push("/order_confirm");
  //   }
  const address = location.state.address;
  async function _handleSubmit(values, action) {
    let updateAddress;
    let defaultAddress;
    if (values.defaultaddress) {
      defaultAddress = true;
    }
    delete values.defaultaddress;
    updateAddress = values;
    updateAddress.user_id = 6;
    updateAddress.id = address.id;
    await Axios.patch("https://frozen-reef-15026.herokuapp.com/api/address", {
      address: updateAddress,
      defaultAddress: defaultAddress,
    });
    let { from } = location.state;
    history.replace(from);
  }

  const CustomTextInput = ({ label, ...props }) => {
    const [field, meta] = useField(props);
    return (
      <>
        <label className="text-gray-600" htmlFor={props.name}>
          {label}
        </label>
        <input
          {...field}
          className="w-full px-3 py-2 text-sm leading-tight text-gray-700 border rounded shadow appearance-none focus:outline-none focus:shadow-outline"
        />
        {meta.touched && meta.error ? (
          <div className="py-1 pl-4 text-white bg-red-400 ">{meta.error}</div>
        ) : null}
      </>
    );
  };

  //   function _sleep(ms) {
  //     return new Promise((resolve) => setTimeout(resolve, ms));
  //   }
  let options = [{ label: "King Market", value: "King Market" }];
  return (
    <div>
      <div className="flex flex-col h-screen ">
        <div className="flex items-center text-white bg-red-500 ">
          <div className="flex items-center justify-center py-2">
            <div className="px-2 " onClick={() => history.goBack()}>
              <svg
                fill="currentColor"
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                viewBox="0 0 24 24"
              >
                <path d="M16.67 0l2.83 2.829-9.339 9.175 9.339 9.167-2.83 2.829-12.17-11.996z" />
              </svg>
            </div>

            <div className="pl-4 ">Edit Address</div>
          </div>
        </div>
        <div className="flex flex-col flex-1 pt-10 overflow-y-auto ">
          <Formik
            initialValues={{
              city: address.city,
              house_no: address.house_no,
              pincode: address.pincode,
              address_detail: address.address_detail,
              defaultaddress: address.defaultaddress,
              delivery_area: address.delivery_area,
            }}
            validationSchema={Yup.object().shape({
              city: Yup.string().required("Please enter city Name"),
              house_no: Yup.string().required("Please Enter valid address"),
              address_detail: Yup.string()
                .min(4, "Enter Correct address")
                .required("Please Enter valid address"),
              delivery_area: Yup.string().required(),
              pincode: Yup.number()
                .typeError("That doesn't look like a  number")
                .positive("A pincode can't start with a minus")
                .integer("A pincode can't include a decimal point")
                .min(500000, "Invalid number")
                .max(600000, "Invalid number")
                .required("pincode is required"),
            })}
            onSubmit={(values, actions) => _handleSubmit(values, actions)}
          >
            {({
              values,
              handleSubmit,
              handleChange,
              setFieldValue,
              errors,
              validateForm,
            }) => (
              <Form className="flex flex-col flex-1 overflow-y-auto ">
                <div className="flex flex-col justify-between flex-1 overflow-y-auto ">
                  <div className="flex flex-col flex-1 mx-4 space-y-2 overflow-y-auto ">
                    <pre>{JSON.stringify(values, 2, null)}</pre>
                    <CustomTextInput
                      label="city"
                      name="city"
                      type="text"
                      onChange={handleChange}
                      value={values.city}
                      placeholder="Enter city Name"
                    ></CustomTextInput>
                    <Select
                      options={options}
                      placeholder="Select Delivery Area"
                      isMulti={false}
                      value={options.find(
                        (i) => i.value === values.delivery_area
                      )}
                      name="delivery_area"
                      label="delivery_area"
                      onChange={(e) => setFieldValue("delivery_area", e.value)}
                    ></Select>
                    <CustomTextInput
                      label="house_no"
                      name="house_no"
                      type="text"
                      value={values.house_no}
                      placeholder="Your house_no"
                    ></CustomTextInput>

                    <CustomTextInput
                      label="address_detail"
                      name="address_detail"
                      type="text"
                      value={values.address_detail}
                      placeholder="Your House address"
                    ></CustomTextInput>
                    <CustomTextInput
                      label="pincode"
                      name="pincode"
                      type="number"
                      value={values.pincode}
                      placeholder="pincode"
                    ></CustomTextInput>
                    <div className="flex items-center pt-5 space-x-4">
                      <input
                        type="radio"
                        onChange={() => setFieldValue("defaultaddress", "true")}
                        value="true"
                        checked={values.defaultaddress === "true"}
                        name="defaultaddress"
                      />
                      <label htmlFor="defaultaddress">
                        Default For Delivery
                      </label>
                    </div>
                    <div className="flex justify-end py-2">
                      <button
                        type="submit"
                        onClick={() =>
                          validateForm().then((e) => {
                            if (!e) {
                              return _handleSubmit(values);
                            }
                            return;
                          })
                        }
                        className="px-4 py-2 mr-2 text-white bg-green-600 rounded "
                      >
                        Save
                      </button>
                    </div>
                  </div>
                </div>
              </Form>
            )}
          </Formik>
        </div>
      </div>
    </div>
  );
}

export default EditAddress;
