import * as actionTypes from "../actions/actionTypes";

export const updateObject = (oldObject, updatedProperties) => {
  return {
    ...oldObject,
    ...updatedProperties,
  };
};

const initialState = {
  cart: [],
  cartDetail: [],
  loading: false,
};

const addToCart = (state, action) => {
  let item = { ...action.item, quantity: 1 };
  const cart = [...state.cart, item];

  const updatedState = {
    cart: cart,
    loading: false,
  };

  return updateObject(state, updatedState);
};

const decrementProduct = (state, action) => {
  let cart = [...state.cart];
  let index = cart.findIndex((c) => c.sku_id === action.sku_id);

  if (cart[index].quantity === 1) {
    cart = cart.filter((c) => c.sku_id !== action.sku_id);
  } else {
    cart[index].quantity = cart[index].quantity - 1;
  }

  const updatedSt = {
    cart: cart,
  };

  return updateObject(state, updatedSt);
};

const incrementProduct = (state, action) => {
  let cart = [...state.cart];

  let productIndex = cart.findIndex((c) => c.sku_id === action.sku_id);
  console.log(productIndex);
  if (productIndex !== -1) {
    cart[productIndex].quantity = cart[productIndex].quantity + 1;
  }

  const updatedSt = {
    cart: cart,
  };

  return updateObject(state, updatedSt);
};

const setCart = (state, action) => {
  console.log(state);
  return updateObject(state, {
    cart: action.cart,
    loading: false,
  });
};
const clearCart = (state, action) => {
  return updateObject(state, {
    cart: [],
  });
};

const setLoading = (state, action) => {
  return updateObject(state, {
    loading: true,
  });
};

const validatedCart = (state, action) => {
  console.log(state, action);
  return updateObject(state, {
    cart: action.data,
    loading: false,
  });
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.ADD_TO_CART:
      return addToCart(state, action);

    case actionTypes.SET_CART:
      return setCart(state, action);
    case actionTypes.INCREMENT_PRODUCT:
      return incrementProduct(state, action);
    case actionTypes.DECREMENT_PRODUCT:
      return decrementProduct(state, action);
    case actionTypes.CART_LOADING:
      return setLoading(state, action);
    case actionTypes.VALIDATED_CART:
      return validatedCart(state, action);
    case actionTypes.CLEAR_CART:
      return clearCart(state, action);
    default:
      return state;
  }
};

export default reducer;
