import Axios from "axios";
import * as actionTypes from "./actionTypes";

export const setProducts = (data) => {
  return {
    type: actionTypes.FETCH_PRODUCTS_SUCCESS,
    products: data,
  };
};

export const setHasMore = (data) => {
  return {
    type: actionTypes.SET_HAS_MORE,
    data,
  };
};
export const fetchMoreProducts = (category_id, product_category_id, page) => {
  return (dispatch, getState) => {
    const brand_filters = getState().productBuilder.brand_filters;
    let brand = [];

    brand = brand_filters.map((i) => {
      return i.value;
    });
    let sort = getState().productBuilder.sort;
    brand = [...new Set([...brand])];
    dispatch(incrementPage());

    dispatch(fetchProductsStart());
    const page = getState().productBuilder.page;
    Axios.post("https://frozen-reef-15026.herokuapp.com/api/products", {
      category_id,
      product_category_id,
      page,
      brand_filters: brand,
      sort,
    })
      .then((response) => {
        if (response.data.products.length > 0) {
          dispatch(setHasMore(response.data.products.length > 0));
          dispatch(setMoreProducts(response.data.products));
        } else {
          dispatch(setLoading(false));
          dispatch(setHasMore(response.data.products.length > 0));
        }
      })
      .catch((error) => {
        dispatch(setHasMore(false));
        dispatch(fetchProductsFailed());
      });
  };
};
export const fetchMoreSearch = () => {
  return (dispatch, getState) => {
    dispatch(incrementPage());
    dispatch(fetchProductsStart());
    Axios.get(
      "https://frozen-reef-15026.herokuapp.com/api/product_suggestions",
      {
        params: {
          q: getState().productBuilder.query,
          page: getState().productBuilder.page,
        },
      }
    )
      .then((response) => {
        if (response.data.length > 0) {
          dispatch(setHasMore(response.data.length > 0));
          dispatch(setMoreSearch(response.data));
        } else {
          dispatch(setLoading(false));
          dispatch(setHasMore(response.data.length > 0));
        }
      })
      .catch((error) => {
        dispatch(fetchProductsFailed());
      });
  };
};

export const setMoreSearch = (data) => {
  return {
    type: actionTypes.SET_MORE_SEARCH,
    data,
  };
};

export const setMoreProducts = (data) => {
  return {
    type: actionTypes.SET_MORE_PRODUCTS,
    data,
  };
};

export const setQuery = (query) => {
  return {
    type: actionTypes.SET_QUERY,
    query,
  };
};
export const setLoading = (data) => {
  return {
    type: actionTypes.SET_LOADING,
    data,
  };
};
export const fetchProductsFailed = () => {
  return {
    type: actionTypes.FETCH_PRODUCTS_FAILED,
  };
};

export const fetchProductsStart = () => {
  return {
    type: actionTypes.FETCH_PRODUCTS_START,
  };
};
export const incrementPage = () => {
  return {
    type: actionTypes.INCREMENT_PAGE,
  };
};
export const resetPage = () => {
  return {
    type: actionTypes.RESET_PAGE,
  };
};
export const resetProducts = () => {
  return {
    type: actionTypes.RESET_PRODUCTS,
  };
};

export const initSearchProduct = (query) => {
  return (dispatch, getState) => {
    dispatch(resetProducts());

    dispatch(resetPage());
    dispatch(fetchProductsStart());
    Axios.get(
      "https://frozen-reef-15026.herokuapp.com/api/product_suggestions",
      {
        params: {
          q: getState().productBuilder.query,
          page: 0,
        },
      }
    )
      .then((res) => {
        dispatch(setProducts(res.data));
        dispatch(setHasMore(res.data.length > 0));
      })

      .catch((err) => {
        if (Axios.isCancel(err)) {
          return;
        } else {
          console.log(err);
        }
      });
  };
};
export const products = (category_id, product_category_id) => {
  return {
    type: actionTypes.INIT_PRODUCT,
    payload: {
      category_id,
      product_category_id,
    },
  };
};
export const setBrand = (data) => {
  return {
    type: actionTypes.SET_BRAND,
    payload: {
      brand: data,
    },
  };
};
export const setBrandFilter = (data) => {
  return { type: actionTypes.SET_BRAND_FILTER, data };
};
export const setSort = (data) => {
  return { type: actionTypes.SET_SORT, data };
};

export const fetchBrandFilter = (category_id, product_category_id) => async (
  dispatch,
  getState
) => {
  const brand_filters = getState().productBuilder.brand_filters;
  let brand = [];

  brand = brand_filters.map((i) => {
    return i.value;
  });
  let sort = getState().productBuilder.sort;
  brand = [...new Set([...brand])];
  dispatch(resetProducts());
  dispatch(resetPage());
  dispatch(fetchProductsStart());

  try {
    const { data } = await Axios.post(
      "https://frozen-reef-15026.herokuapp.com/api/products",
      {
        category_id,
        product_category_id,
        page: 0,
        brand_filters: brand,
        sort,
      }
    );
    dispatch(setProducts(data.products));
    dispatch(setHasMore(data.products.length > 0));
  } catch (e) {
    console.log(e);
  }
};
const resetFilters = () => {
  return { type: actionTypes.RESET_FILTERS };
};
export const initProducts = (category_id, product_category_id, cancelToken) => {
  return async (dispatch) => {
    dispatch(resetProducts());
    dispatch(resetFilters());
    dispatch(resetPage());
    dispatch(fetchProductsStart());

    try {
      const { data } = await Axios.get(
        "https://frozen-reef-15026.herokuapp.com/api/products",
        {
          params: {
            category_id,
            product_category_id,
            page: 0,
          },
        },
        {
          cancelToken: cancelToken,
        }
      );

      dispatch(setProducts(data.products));
      dispatch(setBrand(data.brand));
      dispatch(setHasMore(data.products.length > 0));
    } catch (error) {
      console.log(error);
      if (Axios.isCancel(error)) return;
      dispatch(fetchProductsFailed());
    }
  };
};
