import React, { useRef, useState } from "react";
import { Formik, useField, Form } from "formik";
import * as Yup from "yup";
import Axios from "axios";
import * as actions from "../store/actions/index";
import { useLocation, useHistory, Redirect } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";

import { LoginInput, Button } from "../components/loginInput";
const LoginPage = () => {
  const [step, setStep] = useState(0);
  const isSignedIn = useSelector((state) => state.authBuilder.isSignedIn);

  let location = useLocation();
  let history = useHistory();
  let dispatch = useDispatch();
  const formRef = useRef();

  let validationSchema = [
    Yup.object({
      phoneNumber: Yup.number()
        .moreThan(5000000000, "Please enter a correct Phone Number")
        .lessThan(9999999999, "Please enter a correct Phone Number")
        .required("Please enter a correct Phone Number"),
    }),
    Yup.object({
      otp: Yup.number()
        .lessThan(10000, "Please enter a correct OTP")
        .moreThan(999, "Please enter a correct OTP")
        .required("Please enter a correct OTP"),
    }),
  ];

  const sendOtp = async (
    values,
    { setSubmitting, setValues, setErrors, setFieldValue }
  ) => {
    if (formRef.current) {
      formRef.current.setSubmitting(true);

      if (formRef.current.isValid) {
        console.log(values);
        let url;
        setFieldValue("Retry", "");
        if (values.otp === "") {
          url = "https://frozen-reef-15026.herokuapp.com/api/otp";
          try {
            const { data, status } = await Axios.post(url, {
              phoneNumber: values.phoneNumber,
            });
            if (status === 202) {
              console.log(data);

              setSubmitting(false);

              setStep(1);
            }
          } catch (err) {
            console.log(err);
            if (err.response.data.Retry) {
              setErrors({
                err:
                  "Too Many Requests. " +
                  " Retry after :" +
                  err.response.data.Retry +
                  " secs",
              });
            } else
              setErrors({
                err: err.response.data.message,
              });
            setSubmitting(false);
          }
        } else {
          url = "https://frozen-reef-15026.herokuapp.com/api/validate-otp";
          try {
            const { data } = await Axios.post(url, {
              phoneNumber: values.phoneNumber,
              otp: values.otp,
            });
            if (data) {
              console.log(data);

              setSubmitting(false);
              dispatch(actions.signIn(data));

              let { from } = location.state || { from: { pathname: "/" } };
              dispatch(actions.getCart());
              history.replace(from);
            }
          } catch (err) {
            console.log(err);
            if (err.response.data.Retry) {
              setErrors({
                err:
                  "Too Many Requests. " +
                  " Retry after :" +
                  err.response.data.Retry +
                  " secs",
              });
            } else
              setErrors({
                err: err.response.data.message,
              });
            setSubmitting(false);
          }
        }
      }
    }
  };
  console.log(step);
  const render = () => {
    if (isSignedIn) {
      return <Redirect to="/"></Redirect>;
    } else if (isSignedIn === false) {
      return (
        <div>
          <div className="flex flex-col h-screen ">
            <div className="flex items-center justify-between text-white bg-red-500 ">
              <div className="flex items-center justify-center py-2">
                <div className="px-2 " onClick={() => history.goBack()}>
                  <svg
                    fill="currentColor"
                    xmlns="http://www.w3.org/2000/svg"
                    width="16"
                    height="16"
                    viewBox="0 0 24 24"
                  >
                    <path d="M16.67 0l2.83 2.829-9.339 9.175 9.339 9.167-2.83 2.829-12.17-11.996z" />
                  </svg>
                </div>
                <div className="pl-4 ">Login</div>
              </div>
            </div>

            <div className="flex flex-col flex-1 pt-10 overflow-y-auto ">
              <Formik
                innerRef={formRef}
                validationSchema={validationSchema[step]}
                initialValues={{
                  phoneNumber: "8328515049",
                  otp: "",
                  resend: "false",
                  Retry: "",
                }}
                onSubmit={(values, actions) => sendOtp(values, actions)}
              >
                {({
                  values,
                  validateForm,
                  handleChange,
                  errors,
                  status,
                  setFieldValue,
                  handleSubmit,
                  setErrors,
                  isSubmitting,
                }) => {
                  return (
                    <Form className="flex flex-col flex-1 overflow-y-auto ">
                      <div className="mx-3">
                        {values.Retry && (
                          <div className="py-2 pl-4 my-2 text-red-500 border border-red-600 ">
                            {values.Retry}
                          </div>
                        )}
                        {errors.err && errors ? (
                          <div className="py-2 pl-4 my-2 text-red-500 border border-red-600 ">
                            {errors.err}
                          </div>
                        ) : null}{" "}
                        <LoginInput
                          label="Enter your Phone Number"
                          name="phoneNumber"
                          type="number"
                          value="phoneNumber"
                          disabled={step !== 0}
                          onChange={handleChange}
                          Symbol={true}
                          placeholder="9876543210"
                        ></LoginInput>
                        {step !== 0 ? (
                          <>
                            <LoginInput
                              label="Enter Otp"
                              name="otp"
                              type="number"
                              value="otp"
                              onChange={handleChange}
                              placeholder="4444"
                            ></LoginInput>
                            {values.resend === "false" && (
                              <button
                                type="submit"
                                className="items-center block px-4 py-2 mx-auto mt-4 text-base leading-6 text-white transition duration-150 ease-in-out bg-purple-500 border border-transparent rounded-md shadow-sm focus:outline-none focus:ring-purple-300 hover:bg-purple-300 active:bg-purple-700 "
                                onClick={async () => {
                                  setFieldValue("resend", "true");
                                  let url =
                                    "https://frozen-reef-15026.herokuapp.com/api/resend-otp";
                                  try {
                                    const { data, status } = await Axios.post(
                                      url,
                                      {
                                        phoneNumber: values.phoneNumber,
                                      }
                                    );
                                    if (status === 202) {
                                      console.log(data);

                                      setFieldValue("resend", "false");
                                    }
                                  } catch (err) {
                                    setFieldValue("resend", "false");
                                    console.log(values);
                                    if (err.response.data.Retry) {
                                      setFieldValue(
                                        "Retry",

                                        "Too Many Requests. " +
                                          " Retry after :" +
                                          err.response.data.Retry +
                                          " secs"
                                      );
                                    } else
                                      setFieldValue(
                                        "Retry",
                                        err.response.data.message
                                      );
                                  }
                                  return;
                                }}
                              >
                                Resend Otp
                              </button>
                            )}
                            {values.resend === "true" && (
                              <button className="inline-flex items-center px-3 py-2 mx-20 mt-4 text-base leading-6 text-white transition duration-150 ease-in-out bg-indigo-500 border border-transparent rounded-md shadow-sm focus:outline-none focus:ring-indigo-300 hover:bg-indigo-300 active:bg-indigo-700 ">
                                <svg
                                  class="animate-spin -ml-1 mr-3 h-5 w-5 text-white"
                                  xmlns="http://www.w3.org/2000/svg"
                                  fill="none"
                                  viewBox="0 0 24 24"
                                >
                                  <circle
                                    class="opacity-25"
                                    cx="12"
                                    cy="12"
                                    r="10"
                                    stroke="currentColor"
                                    stroke-width="4"
                                  ></circle>
                                  <path
                                    class="opacity-75"
                                    fill="currentColor"
                                    d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                                  ></path>
                                </svg>
                                Processing
                              </button>
                            )}
                          </>
                        ) : null}{" "}
                      </div>
                      {!isSubmitting ? (
                        <Button
                          text={step === 0 ? "Get OTP" : "Submit"}
                          // onClick={handleSubmit}
                        ></Button>
                      ) : (
                        <button className="inline-flex items-center px-3 py-2 mx-20 mt-4 text-base leading-6 text-white transition duration-150 ease-in-out bg-indigo-500 border border-transparent rounded-md shadow-sm focus:outline-none focus:ring-indigo-300 hover:bg-indigo-300 active:bg-indigo-700 ">
                          <svg
                            class="animate-spin -ml-1 mr-3 h-5 w-5 text-white"
                            xmlns="http://www.w3.org/2000/svg"
                            fill="none"
                            viewBox="0 0 24 24"
                          >
                            <circle
                              class="opacity-25"
                              cx="12"
                              cy="12"
                              r="10"
                              stroke="currentColor"
                              stroke-width="4"
                            ></circle>
                            <path
                              class="opacity-75"
                              fill="currentColor"
                              d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                            ></path>
                          </svg>
                          Processing
                        </button>
                      )}
                    </Form>
                  );
                }}
              </Formik>
            </div>
          </div>
        </div>
      );
    } else {
      return <div>loading</div>;
    }
  };
  return <>{render()}</>;
};

export default LoginPage;
