import React, { useEffect } from "react";

import ReactDOM from "react-dom";

import { useLocation, useHistory } from "react-router-dom";
const queryString = require("query-string");

function ProductFilter({ setToggleProductFilter, toggleProductFilter }) {
  const filter = document.getElementById("filter");
  const root = document.getElementById("root");
  // const sorting= useSelector((state) => state.productBuilder.sort);

  let location = useLocation();
  const sorting = queryString.parse(location.search).sort || "popularity";

  let history = useHistory();

  let pathname = location.pathname;

  useEffect(() => {
    document.body.classList.add("overflow-hidden");
    root.classList.add("static");
    filter.classList.add(
      "h-screen",

      "flex",
      "items-center",
      "inset-0",
      "justify-center"
    );
    return () => {
      document.body.classList.remove("overflow-hidden");
      root.classList.remove("static");
      filter.classList.remove(
        "h-screen",

        "flex",
        "items-center",
        "inset-0",
        "justify-center"
      );
    };
  });
  const dispatchSort = (e) => {
    let query = queryString.parse(location.search, { arrayFormat: "bracket" });
    query.sort = e.target.value;
    query.page = 0;
    query = queryString.stringify(query, { arrayFormat: "bracket" });
    history.push({
      pathname: pathname,
      search: query,
    });
    // dispatch(actions.setSort(e.target.value));
    // dispatch(actions.fetchBrandFilter(category, subCategory));
    setToggleProductFilter((e) => !e);
  };
  return ReactDOM.createPortal(
    <>
      {" "}
      <div
        className="absolute inset-0 z-10 bg-black opacity-25 dark:opacity-80"
        onClick={setToggleProductFilter}
      ></div>
      <div
        className={
          toggleProductFilter
            ? "fixed bottom-0 z-20 flex flex-col w-full py-4 mb-4 opacity-100 transition-opacity duration-500 bg-white dark:text-gray-400 dark:bg-mine-shaft-500"
            : "fixed bottom-0 z-20 flex flex-col w-full py-4 mb-4 opacity-0 pointer-events-none  transition-opacity duration-500 bg-white dark:bg-mine-shaft-500 dark:text-gray-400 "
        }
      >
        <div className="px-4 py-2 font-bold text-gray-600 border-b border-gray-500 dark:text-gray-400 ">
          SORT BY
        </div>
        <div className="flex flex-col px-4 py-4 space-y-2">
          <div className="flex items-center justify-between font-bold">
            <span> Popularity</span>
            <span>
              <input
                type="radio"
                name="sort-filter"
                checked={sorting === "popularity"}
                value="popularity"
                onChange={dispatchSort}
              />
            </span>
          </div>
          <div className="flex items-center justify-between font-bold">
            <span> Price -- Low to High</span>
            <input
              type="radio"
              name="sort-filter"
              value="low-to-high"
              checked={sorting === "low-to-high"}
              onChange={dispatchSort}
            />
          </div>
          <div className="flex items-center justify-between font-bold">
            <span> Price -- High to Low</span>
            <input
              type="radio"
              name="sort-filter"
              value="high-to-low"
              checked={sorting === "high-to-low"}
              onChange={dispatchSort}
            />
          </div>
          <div className="flex items-center justify-between font-bold">
            <span> Newest First</span>
            <input
              type="radio"
              name="sort-filter"
              value="new-first"
              checked={sorting === "new-first"}
              onChange={dispatchSort}
            />
          </div>

          <div className="flex items-center justify-between font-bold">
            <span> Discount</span>
            <input
              type="radio"
              name="sort-filter"
              value="discount"
              checked={sorting === "discount"}
              onChange={dispatchSort}
            />
          </div>
        </div>
      </div>
    </>,
    document.getElementById("filter")
  );
}

export default ProductFilter;
