import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import * as actions from "../store/actions";
import { SkeletonComponent } from "../utils/SkeletonComponent";

function Profile() {
  let dispatch = useDispatch();

  let history = useHistory();
  const user = useSelector((state) => state.authBuilder.phoneNumber);
  const [name, _] = useState(user);
  let addresses = useSelector((state) => state.authBuilder.address);
  let loading = useSelector((state) => state.authBuilder.loading);
  const render = () => {
    if (loading) {
      return (
        <div className="divide-y divide-gray-200 ">
          <div className="px-4 py-2">
            <div className="animate-pulse">
              <SkeletonComponent type="long"></SkeletonComponent>
            </div>
          </div>
          <div className="px-4 py-2">
            <div
              className="animate-pulse"
              style={{
                animationFillMode: "backwards",
                animationDelay: "150ms",
              }}
            >
              <SkeletonComponent type="short"></SkeletonComponent>
            </div>
          </div>
          <div className="px-4 py-2">
            <div
              className="animate-pulse"
              style={{
                animationFillMode: "backwards",
                animationDelay: "300ms",
              }}
            >
              <SkeletonComponent type="long"></SkeletonComponent>
            </div>
          </div>
        </div>
      );
    } else {
      return (
        <div className="flex flex-col pt-4 space-y-3">
          <div className="px-4 py-2 mx-8 ">
            <label
              htmlFor={"Phone Number"}
              className="block py-4 font-medium text-gray-700"
            >
              Phone Number
            </label>
            <input
              disabled={true}
              value={name}
              className="block w-full py-2 pl-4 mb-4 bg-white border border-gray-300 rounded-md pr-7 focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
            ></input>
            <div className="address">
              {addresses && addresses.length > 0 && (
                <div className="px-2 py-1 font-semibold text-white bg-red-500">
                  Address List
                </div>
              )}
              <div className="flex flex-col divide-y-2 divide-gray-200">
                {addresses &&
                  addresses.length > 0 &&
                  addresses.map((i) => {
                    return (
                      <div className="flex flex-col py-1 " key={i.id}>
                        <div className="px-4 pt-3 bg-white">
                          <div
                            className="flex "
                            onClick={() =>
                              history.push({
                                pathname: "/edit_address",
                                state: { address: i, from: "/profile" },
                              })
                            }
                          >
                            <div>
                              <svg
                                xmlns="http://www.w3.org/2000/svg"
                                width="24"
                                height="24"
                                viewBox="0 0 24 24"
                              >
                                <path d="M12 0c-4.198 0-8 3.403-8 7.602 0 4.198 3.469 9.21 8 16.398 4.531-7.188 8-12.2 8-16.398 0-4.199-3.801-7.602-8-7.602zm0 11c-1.657 0-3-1.343-3-3s1.343-3 3-3 3 1.343 3 3-1.343 3-3 3z" />
                              </svg>
                            </div>
                            <div className="w-full pl-3">
                              <div className="font-bold house_no">
                                {i.house_no}
                              </div>
                              <div>{i.address}</div>
                              <div>
                                {i.delivery_area},{i.city},{i.pincode}
                              </div>
                            </div>
                          </div>
                          <div className="flex items-center justify-end">
                            <button
                              className="mr-4 font-bold text-red-500 "
                              onClick={() =>
                                dispatch(actions.deleteAddress(i.id))
                              }
                            >
                              Delete
                            </button>
                          </div>
                        </div>
                      </div>
                    );
                  })}
              </div>
            </div>
          </div>
        </div>
      );
    }
  };

  return (
    <>
      <div>
        <div className="h-full ">
          <div className="flex flex-col ">
            <div className="flex items-center justify-between py-4 bg-blue-500">
              <div className="px-2 ">
                <svg
                  onClick={() => history.goBack()}
                  fill="currentColor"
                  className="inline"
                  xmlns="http://www.w3.org/2000/svg"
                  width="16"
                  height="16"
                  viewBox="0 0 24 24"
                >
                  <path d="M16.67 0l2.83 2.829-9.339 9.175 9.339 9.167-2.83 2.829-12.17-11.996z" />
                </svg>
                <div className="inline pl-4 font-mono font-bold">Profile</div>
              </div>

              <div
                className="flex items-center justify-center px-2 py-1 mx-4 text-white bg-gray-800 rounded"
                onClick={() =>
                  history.push({
                    pathname: "/add_address",
                    state: { from: "/profile" },
                  })
                }
              >
                <span className="pr-3 ">Add Address</span>
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  className="inline w-8 h-8"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWdth="2"
                    d="M12 6v6m0 0v6m0-6h6m-6 0H6"
                  />
                </svg>
              </div>
            </div>
            {render()}
          </div>
        </div>
      </div>
    </>
  );
}

export default Profile;
