import React from "react";
import ReactDOM from "react-dom";
import "./styles/tailwind.css";
import App from "./App";
import { createStore, combineReducers, compose, applyMiddleware } from "redux";
import { Provider } from "react-redux";
import cartBuilder from "./store/reducer/cart";
import category from "./store/reducer/category";
import products from "./store/reducer/products";
import search from "./store/reducer/search";
import * as serviceWorker from "./serviceWorker";
import thunk from "redux-thunk";
import auth from "./store/reducer/auth";
import order from "./store/reducer/order";
import { QueryClient, QueryClientProvider } from "react-query";
const queryClient = new QueryClient();
// import createSageMiddleware from "redux-saga";
// import rootSaga from "./sagas";

// const sagaMiddleware = createSageMiddleware();
const middlewares = [thunk];

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const rootReducer = combineReducers({
  cartBuilder: cartBuilder,
  categoryBuilder: category,
  productBuilder: products,
  authBuilder: auth,
  searchBuilder: search,
  orderBuilder: order,
});
const store = createStore(
  rootReducer,
  composeEnhancers(applyMiddleware(...middlewares))
);
// sagaMiddleware.run(rootSaga);

ReactDOM.render(
  <Provider store={store}>
    <QueryClientProvider client={queryClient}>
      <App></App>
    </QueryClientProvider>
  </Provider>,
  document.querySelector("#root")
);
serviceWorker.register();
