import * as actionTypes from "./actionTypes";
import Axios from "axios";
Axios.defaults.withCredentials = true;

export const OrderSuccess = (id) => {
  return {
    type: actionTypes.CREATE_ORDER_SUCCESS,
    orderId: id,
  };
};

export const OrderFail = (error) => {
  return {
    type: actionTypes.CREATE_ORDER_FAILED,
    error: error,
  };
};

export const OrderStart = () => {
  return {
    type: actionTypes.CREATE_ORDER_START,
  };
};

export const Order = (orderData, token) => {
  return (dispatch) => {
    dispatch(OrderStart());

    Axios.post("https://frozen-reef-15026.herokuapp.com/api/order", orderData)
      .then((response) => {
        dispatch(OrderSuccess(response.data.id));
      })
      .catch((error) => {
        dispatch(OrderFail(error));
      });
  };
};

export const purchaseInit = () => {
  return {
    type: actionTypes.PURCHASE_INIT,
  };
};
export const purchaseReset = () => {
  return {
    type: actionTypes.PURCHASE_RESET,
  };
};

export const fetchOrdersSuccess = (orders) => {
  return {
    type: actionTypes.FETCH_ORDERS_SUCCESS,
    orders: orders,
  };
};

export const fetchOrdersFail = (error) => {
  return {
    type: actionTypes.FETCH_ORDERS_FAIL,
    error: error,
  };
};

export const fetchOrdersStart = () => {
  return {
    type: actionTypes.FETCH_ORDERS_START,
  };
};

export const fetchOrders = () => {
  return async (dispatch, getState) => {
    dispatch(fetchOrdersStart());
    const userId = getState().authBuilder.userId;
    const { data } = await Axios.get(
      `https://frozen-reef-15026.herokuapp.com/api/orders?q=${userId}`
    );
    dispatch(fetchOrdersSuccess(data));
  };
};
